
.customers {
    @extend %inner-layout;
    position: relative;
    background: $color-light-background;
    z-index: 1;
    overflow: hidden;
    
    @include breakpoint(tablet) {
        height: 620px;
    }

    @include breakpoint(desktop) {
        height: 790px;
    }

    .layout {
        @extend %align-middle;

        @include breakpoint(tablet) {
            height: 100%;
        }

        ul {
            margin-bottom: 63px;

            @include breakpoint(tablet) {
                padding-left: 101px - 22px;
                margin-bottom: 0;
            }

            @include breakpoint(desktop) {
                padding-left: 0;
            }
        }
    }

    h2 {
        @extend %second-header-light;
        padding-top: 188px;

        @include breakpoint(tablet) {
            padding-top: 306px;
        }

        @include breakpoint(desktop) {
            padding-top: 0;
            text-align: left;
            display: inline-block;
            vertical-align: middle;
        }

        span {
            &:first-child {
                display: inline;
            }
        }

        &:after {
            content: none;
            padding-bottom: 0;
        }
    }

    ul {

        @include breakpoint(desktop) {
            position: absolute;
            //right: 0;
            left: 50%;
            top: 453px;
        }
        
    }

    li {
        @extend %page-text;
        @extend %li-bullet;
        color: $color-light-text;
        font-weight: 400;
    }
}

.customers__background-text {
    z-index: -1;

    white-space: nowrap;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    text-align: center;
    padding: rem(74px) 0 0 0;

    span {
        color: #efefef;
        font-weight: 700;
        text-transform: uppercase;
        position: relative;
        font-size: 190px;
        display: inline-block;
        vertical-align: top;


        &:after {
            content: '';
            position: absolute;
            top: 0;
            bottom: 0;
            left: rem(-50px);
            right: rem(-50px);
            background: linear-gradient(180deg, rgba(255, 255, 255, 0), #fff);
        }
        
        @include breakpoint(tablet) {
            font-size: rem(520px);
            letter-spacing: rem(-60px);
        }

    }
}
