.screens {
    padding-bottom: 50px;
    font-size: 14px;
    background: #fff;
}
.screens-wrap {
    margin: 0 auto;
    background-size: contain;
    background-position: center top;
    background-repeat: no-repeat;
    background-image: url('../img/views/iphone.jpg');
    width: 460px;
    height: 690px;
    position: relative;
    margin-left: -230px;
    left: 50%;
    padding-top: 57px;

    @include breakpoint(small){
        left: auto;
       /* width: 620px;
        height: 795px;*/
        width: 97vw;
        height: 125vw;
        background-image: url('../img/views/ipad-vert.jpg');
        padding-top: 11vw;
        margin: 0 auto;
    }

    @include breakpoint(tablet) {
        /*width: 760px;
        height: 610px;
        padding-top: 56px;*/
        width: 95vw;
        height: 81vw;
        padding-top: 8.2vw;
        background-image: url('../img/views/ipad.jpg');
    }

    @include breakpoint(desktop) {
        /*width: 1002px;
        height: 678px;
        padding-top: 21px;*/
        width: 80vw;
        height: 61vw;
        padding-top: 1.7vw;
        background-image: url('../img/views/monitor.jpg');
    }

    @include breakpoint(xdesktop) {
       /* width: 1260px;
        height: 852px;
        padding-top: 27px;*/

    }

    @include breakpoint(large) {
        /*width: 1480px;
        height: 1000px;
        padding-top: 32px;*/
    }
}
.work-frame {
    display: block;
    width: 320px;
    height: 568px;
    border: none;
    overflow: hidden;
    margin: 0 auto;
    transform: scale(0.85);

    code {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
    }

    @include breakpoint(small){
        /*width: 492px;
        height: 652px;*/
        width: 77.5vw;
        height: 103vw;
        transform: scale(1);
    }

    @include breakpoint(tablet) {
        /*width: 597px;
        height: 451px;*/
        width: 77.7vw;
        height: 58.5vw;
    }
    @include breakpoint(desktop) {
        /*width: 960px;
        height: 532px;*/
        width: 76.7vw;
        height: 42.6vw;
    }
    @include breakpoint(xdesktop) {
        /*width: 1204px;
        height: 667px;*/

    }
    @include breakpoint(large) {

        /*width: 1412px;
        height: 783px;*/
    }
}

.screens-item {
    display: none;

    &.active {
        display: block;
    }
}