.header {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: 2;

    &.index {
        .logo {
            background: none;
            left: 0;
            right: 0;
            top: 0;
            margin: auto;

            @include breakpoint((tablet, large)) {
                width: 82px;
                height: 82px;
                img {
                    width: 42px;
                    height: 42px;
                }
            }
        }
    }
}

.logo {
    background-color: $color-dark-background;
    position: absolute;
    width: 86px;
    height: 86px;

    @include breakpoint(tablet){
        width: rem(172px);
        height: rem(172px);
    }


    img {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        width: 38px;
        height: 38px;

        @include breakpoint(tablet){
            width: rem(77px);
            height: rem(76px);
        }
    }
}

%menu-button-line {
    width: 100%;
    position: absolute;
    height: 3px;
    background-color: #888d9d;
    left: 0;
    right: 0;

    &:before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        width: 0;
        background-color: #ffffff;

        @include breakpoint(desktop) {
            transition: width 0.3s ease-out;
            will-change: width;
        }

    }
}

.menu-button {
    @extend %menu-button-size-and-position;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transform: translate3d(0, 0, 0);
    z-index: 2;
    
    div {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        width: 27px;
        height: 19px;
        
        @include breakpoint(tablet) {
            width: 34px;
            height: 21px;
        }

        span {
            @extend %menu-button-line;

            &:first-child {
                top: 0;
            }

            &:nth-child(2) {
                top: 0;
                bottom: 0;
                margin: auto;

                &:before {
                    @include breakpoint(desktop) {
                        transition-delay: 0.1s;
                    }
                }
            }

            &:last-child {
                bottom: 0;

                &:before {
                    @include breakpoint(desktop) {
                        transition-delay: 0.2s;
                    }
                }
            }
        }
    }

    &:hover {

        @include breakpoint(desktop) {
            div {
                span {
                    &:before {
                        width: 100%;
                    }
                }
            }
        }

    }

}
