.fd__project-list {
    $titleFontSizeSmall: 36px;
    $titleFontSizeFull: 80px;
    $descrFontSizeFull: $font-size;
    $tDuration: 1s;
    $tEasing: ease-out;
    $tDelay: $tDuration * 0.2;
    $scaleFrom: 1;
    $moveFrom: 30%;
    $textMoveFrom: $moveFrom * 0.3;

    $tDuration2: 2s;
    $tEasing2: ease-in-out;
    $tDelay2: $tDuration * 0.1;
    $scaleFrom2: 0.7;
    transform: translate3d(0,0,0);
    backface-visibility: hidden;

    .pl {
        $itemHeight: 344px;
        transform:translate3d(0,0,0);
        backface-visibility: hidden;

        &-list {
            position: relative;
            list-style-type: none;
            overflow: hidden;
            transition: transform $tEasing $tDuration, opacity $tEasing $tDuration;
            transform: translate3d(0,0,0);
            backface-visibility: hidden;

            opacity: 1;

            //  
            &.hidden {
                opacity: 0;
                $baseOffset: 300px;
                $locOffset: 200px;
                @for $i from 0 through 4 {
                    .pl-item:nth-child(#{$i + 1}) {
                        transform: translate3d(0, $itemHeight * $i + $baseOffset + $locOffset * $i, 0) !important;
                    }
                }
                transform: translate3d(0,0,0);
                backface-visibility: hidden;

            }
        }

        &-item {
            height: $itemHeight;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            overflow: hidden;
            text-align: center;
            z-index: 1;
            white-space: nowrap;
            transition: top $tEasing2 $tDuration2;
            transform-origin: 50% 0%;
            transform: translate3d(0,0,0);
            backface-visibility: hidden;
            border-top: 1px solid $color-dark-background;
            border-bottom: 1px solid $color-dark-background;
            display: flex;
            align-items: center;


            &.filtered {
                transform: scale($scaleFrom2, $scaleFrom2);
                opacity: 0;
                z-index: 0;
            }

            &:before,
            &:after {
                content: "";
                position: absolute;
                right: -1px;
                left: 0;
                top: 0;
                bottom: 0;
                z-index: 1;
                background: rgba(0,0,0,0.3);
                transform: translate3d(0,0,0);
                backface-visibility: hidden;
                overflow: hidden;

                @include breakpoint(small){
                    right: -1px;
                    left: 50%;
                    background: linear-gradient(120deg, rgba(255,255,255,0) -35%, rgba(1,0,0,1) 100%);
                }
            }

            &:before {

                @include breakpoint(small){
                    left: -1px;
                    right: 50%;
                    transform: scale(-1,1);
                }
            }


            /*&:after {
                content: "";
                display: inline-block;
                margin-right: -4px;
                height: 100%;
                vertical-align: middle;
            }*/
        }
        &-image-wrap {
            position: absolute;
            top: 0;
            bottom: 0;
            left: -100%;
            width: 300%;
            text-align: center;
            background-size: cover !important;
            background-position: center center !important;

            img {
                object-fit: cover;
                height: 100%;
                width: 100%;
                transform:translate3d(0,0,0);
                backface-visibility:hidden;

                &.invisible {
                    visibility: hidden;
                }

                @include breakpoint(tablet) {
                    object-fit: contain;
                }
            }

            @include breakpoint(tablet) {
                background-size: contain !important;
            }
        }
        &-cell {
            width: 99%;
            position: relative;
            display: inline-block;
            vertical-align: middle;
            z-index: 1;
            background-color: rgba(0,0,0,0);
            transition:background-color ease-out 0.3s;
            transform:translate3d(0,0,0);
            backface-visibility:hidden;
            text-align: center;
            z-index: 2;

        }
        &-cell-wrap {
            display: inline-block;
            //margin: 0 13vw;
            text-align: left;

            @include breakpoint(tablet) {
                width: 640px;
                margin: 0 auto;
            }
            @include breakpoint(desktop) {
                width: rem(1020px);
            }
        }
        &-header {
            margin-bottom: 40px;
        }
        &-title {
            @extend %page-second-header;
            color: $color-light-background;
            text-transform: uppercase;
            text-align: center;
            white-space: normal;

            &:first-child {
                padding-top: 0;
            }
        }
        &-description {
            @extend %page-text;
            padding-top: 4px;
            white-space: normal;
            text-align: center;

            br {
                display: none;
            }
        }
        &-btns-set {
            text-align: center;
            margin-right: 2.0833vw;

            .btn {
                //width: 220px;
                //height: 50px;
                //line-height: 50px;
                //font-size: 18px;
            }

            @include breakpoint(tablet) {
                text-align: right;
            }
        }
        &-btns-set-row {
            display: block;
            margin-bottom: 11px;
        }
    }

