    @include breakpoint(tablet){
        .pl {
            &-cell-wrap {
                text-align: justify;
                margin: 0 7vw;

                &:after {
                    content: "";
                    width: 100%;
                    display: inline-block;
                }
            }
            &-header {
                display: inline-block;
                vertical-align: middle;
                white-space: normal;
                width: 50%;
                text-align: left;
                margin-bottom: 0;
            }
            &-title {
                //font-size: $titleFontSizeFull;
                //line-height: $titleFontSizeFull;
                text-align: left;
            }
            &-description {
                padding-top: 30px;
                text-align: left;
            }
            &-btns-set {
                vertical-align: middle;
                white-space: normal;
                width: 50%;
                display: inline-block;
                //text-align: center;

                .pl-btns-set-row {
                    display: inline-block;
                    margin-left: 52px;
                    margin-bottom: 0;

                    &:first-child {
                        margin-left: 0;
                    }
                }

                .btn {
                    margin-top: 5px;
                    margin-bottom: 5px;
                }
            }
        }
    };

    @include breakpoint(tablet){
        .pl {
            &-title {
                //font-size: round($titleFontSizeFull * 0.9);
                //line-height: round($titleFontSizeFull * 0.9);
                text-align: left;
            }
            &-btns-set {
                .pl-btns-set-row {
                    margin-bottom: 11px;

                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
        }
    };

    @include breakpoint(full) {
        .pl {
            &-image-wrap {
                left: 0;
                right: 0;
                width: auto;

                img {
                    width: 100%;
                    height: auto;
                }
            }
        }
    }
}


