html, body {
    height: 100%;
}

html {
    overflow: hidden;
    font-size: 48px;

    @include breakpoint(tablet) {
        font-size: 95px;
    }

    @include breakpoint(desktop) {
        overflow: auto;
        font-size: 95px;
    }

    @include breakpoint(large) {
        font-size: $root-font-size;
    }
}

body {
    overflow: hidden;

    @include breakpoint(desktop) {
        overflow: auto;
        overflow: initial;
    }
}

@include breakpoint(ios) {
    @media (max-width: 748px) {
        *:not(html) {
            -webkit-transform: translate3d(0, 0, 0);
        }
    }
}

.site-wrap {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    overflow: auto;

    @include breakpoint(ios) {
        -webkit-overflow-scrolling: touch;
        -webkit-backface-visibility: hidden;
    }

    @include breakpoint(desktop) {
        position: relative;
        top: auto;
        bottom: auto;
        left: auto;
        right: auto;
        overflow: visible;
        overflow: inherit;
        height: 100%;
    }

    &.loading {
        opacity: 0;
        transition: opacity 1s ease-out;
    }

    &.loaded {
        opacity: 1;

        .full-height {
            &:before {
                opacity: 1;
            }
            /*&:before {
                opacity: 1;
                top: 21%;
                bottom: 71%;

                @include breakpoint(small){
                    //bottom: 76%;
                    @media (max-height: 639px) {
                        //content: normal;
                    }
                }

                @include breakpoint(tablet) {
                    top: 19%;
                    bottom: 73%;
                    @media (max-height: 650px) {
                        content: normal;
                    }
                }

                @include breakpoint(desktop) {
                    top: 20%;
                    bottom: 71%;
                }

                !*@include breakpoint(xdesktop) {
                    top: 17%;
                    bottom: 73%;
                }*!

                @include breakpoint(large) {
                    top: 18%;
                    bottom: 73%;
                    @media (max-height: 800px) {
                        content: normal;
                    }
                }
            }*/
        }
    }

    &.ready {
        .fd__index-title {
            opacity: 1;
            transform: translate3d(0, 0, 0);
        }
    }

}

.main-wrap {
    overflow: hidden;
    @include breakpoint(desktop) {
        min-width: $min-width;
    }

    @extend %clear-fix;

    &:after {
        height: $footer-height;
    }
}

.layout {
    position: relative;
    margin: auto;
}

%slide-bottom-shadow {
    z-index: 1;

    &:before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        background-image: linear-gradient(to top, black 0%, transparent 100%);
        height: vh(513px, 960 / 100 * 1px);
        z-index: 1;
    }
}
%another-header {
    color: $color-dark-header;
    font-weight: 300;
    line-height: 1.53;
    text-transform: uppercase;
    position: relative;
    letter-spacing: normal;
    padding-bottom: rem(44px);
    margin-bottom: rem(55px);
    font-size: rem(36px);

    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        background-color: #555555;
        height: 1px;
        width: 200px;
    }
}

.full-height {
    height: 100%;
    background-color: #ffffff;
    position: relative;
    overflow: hidden;
    min-height: 320px;

    @include breakpoint(tablet) {
        min-height: 500px;
    }

    video,
    picture {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        will-change: transform;
    }

    video {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    > img {
        
    }

    &.inner {
        @extend %slide-bottom-shadow;
    }

    &.index {
        background: url(../img/bg/index-480.jpg) no-repeat center center;
        background-size: cover;

        @media (min-height: 736px) {
            background-image: url(../img/bg/index-736.jpg);
        }

        @media (min-height: 900px) {
            background-image: url(../img/bg/index-1200.jpg);
        }

        .align-center {
            //margin: 0 0 rem(85px);
            //margin: 0 0 rem(65px);
            margin: 0 0 6vh;
            @include breakpoint(small){
                margin: 0 0 8vh;
            }
            @include breakpoint(tablet) {
                margin-bottom: 7.2vh;
            }
        }

        .button {
            position: relative;
            //min-width: 242px;
            z-index: 1;

            @include breakpoint(tablet) {
                //min-width: 332px;
            }
        }

        .middle-text {
            z-index: 2;
            margin: rem(135px) 0 0 0;

            @include breakpoint(tablet) {
                margin-top: rem(205px);
            }
        }

        $tlEasing: ease-out;
        $tlDuration: 1s;
        $icWordsDelay: 1.5s;

        &:before {

            z-index: 1;
            width: 1px;
            position: absolute;
            left: 50%;
            background: #787776;
            margin: 0;
            opacity: 0;
            transition: opacity $tlEasing $tlDuration, bottom $tlEasing $tlDuration, top $tlEasing $tlDuration, height $tlEasing $tlDuration;
            transition-delay: #{$tlDuration * 0.5};

            top: 24vh;
            bottom: auto;
            height: 45px;
            margin-top: -23px;
            content: "";

            @include breakpoint(small){
                top: 22vh;

                @media (max-height: 600px) {
                    top: 24vh;
                }
            }

            @include breakpoint(tablet) {
                height: rem(90px);
                margin-top: rem(-45px);
                top: 24vh;

                @media (max-height: 680px) {
                    content: normal;
                }
            }

            @include breakpoint(desktop) {
                top: 21.5vh;
                content: "";

                @media (max-height: 768px) {
                    top: 22.5vh;
                }
                @media (max-height: 540px) {
                    content: normal;
                }
            }

            @include breakpoint(large) {
                top: 22.7vh;
                content: "";

                @media (max-height: 800px) {
                    top: 25.5vh;
                }
                @media (max-height: 768px) {
                    content: normal;
                }
            }

            @media (max-height: 520px) {
                top: 27.5vh;
            }
            @media (max-height: 400px) {
                top: 28.5vh;
            }
            @media (max-height: 360px) {
                content: normal;
            }

        }
    }

    &.contacts {
        height: auto;

        .full-height__layout {
            background: url(../img/bg/contact-bg.png) no-repeat center bottom;
            background-size: cover;
        }

        .middle-text {
            padding-bottom: rem(210px);
            h1 {
                @extend %another-header;
            }

            address {
                font-style: normal;
                display: block;
                margin: 0 0 rem(91px);

                span,
                a {
                    @extend %page-text;
                    display: block;
                    margin: 0 0 rem(10px);
                }
            }
        }
    }

    &.form {
        //min-height: 100%;
        height: auto;

        .full-height__layout {
            background: url(../img/bg/form-bg.jpg) no-repeat center bottom;
            background-size: cover;
            //padding: rem(275px) 0 rem(148px);
            padding: 0 0 rem(148px);
        }

        .middle-text {
            padding: vh(158px, nth($v-height, 4)) 0 0 0;

            h1 {
                @extend %another-header;
                max-width: 857px;
                padding-left: 20px;
                padding-right: 20px;
                margin-left: auto;
                margin-right: auto;
            }

            h2 {
                @extend %page-third-header;
                color: $color-dark-light;
                font-weight: 300;
                line-height: 2;
                max-width: rem(546px + 40px);
                padding: 0 rem(20px);
                margin-left: auto;
                margin-right: auto;
                margin-bottom: rem(82px);
            }


            @include breakpoint(tablet) {
                padding: vh(123px, nth($v-height, 3)) 0 0 0;
            }

            @include breakpoint(large) {
                padding: vh(254px, nth($v-height, 1)) 0 0 0;
            }
        }
    }
}
.portfolio-layout {
    .full-height__layout {
        background: url(../img/bg/portfolio-bg.jpg) no-repeat center;
        background-size: cover;
    }
    .middle-text {
        padding: 86px 0 rem(80px) 0;

        h1 {
            @extend %another-header;
        }

        @include breakpoint(small){
            padding-top: rem(190px);
            padding-bottom: rem(25px);
        }
    }
}
.work-layout {
    position: relative;
    .full-height__layout {
        background: url(../img/bg/work-bg.jpg) no-repeat center;
        background-size: cover;
    }
    .middle-text {
        padding: rem(70px) 0 rem(80px) 0;

        h1 {
            @extend %another-header;
        }

        @include breakpoint(small){
            padding-bottom: rem(25px);
        }
    }
}
.white-layout {
    background: $color-light-background;
}
.full-height__layout {
    white-space: nowrap;
    height: 100%;
    text-align: center;
    z-index: 1;
    position: relative;
    will-change: opacity, transform;
}
