.fd__logos {
    text-align: left;
    padding-top: rem(90px);
}

.fd__logos-list {
    font-size: 0;
    line-height: 0;
}

.fd__logos-item {
    width: 50%;
    text-align: center;
    display: inline-block;
    vertical-align: middle;
    font-size: 12px;
    line-height: normal;
    padding: rem(36px) 0;

    img {
        max-width: 60%;
        height: auto;
        @include breakpoint(small){
            max-width: 70%;
        }
        @include breakpoint(desktop) {
            max-width: 90%;
            height: auto;
        }
    }

    @include breakpoint(small){
        width: 31.33%;
        margin: 0 1%;
    }

    @include breakpoint(desktop) {
        width: 18%;
        margin: 0 1%;
    }
}