@import "functions";

//100 = thin
//200 = extra-light
//300 = light
//400 = normal, book
//500 = medium
//600 = demi-bold
//700 = bold
//800 = heavy
//900 = black

$font-verdana: Verdana, Geneva, sans-serif;
$font: 'Acrom', $font-verdana;
$input-font: $font-verdana;
$font-size: 0;
$input-size: 15px;
$line-height: 1;

@font-face {
    font-family: 'Acrom';
    src: url(../fonts/acrom.thin.woff2),
         url(../fonts/acrom.thin.woff);
    font-weight: 100;
    font-style: normal;
}

@font-face {
    font-family: 'Acrom';
    src: url(../fonts/acrom.light.woff2),
         url(../fonts/acrom.light.woff);
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: 'Acrom';
    src: url(../fonts/acrom.regular.woff2),
         url(../fonts/acrom.regular.woff);
    font-weight: normal;
    font-style: normal;

}

@font-face {
    font-family: 'Acrom';
    src: url(../fonts/acrom.medium.woff2),
         url(../fonts/acrom.medium.woff);
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'Acrom';
    src: url(../fonts/acrom.bold.woff2),
         url(../fonts/acrom.bold.woff);
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Acrom';
    src: url(../fonts/acrom.extra.bold.woff2),
         url(../fonts/acrom.extra.bold.woff);
    font-weight: 800;
    font-style: normal;
}

%content {
    padding: rem(93px, 115px) 0 rem(94px, 115px);

    h1 {
        @extend %page-header;
    }

    h2 {
        @extend %page-second-header;
    }

    h3 {
        @extend %page-third-header;
    }

    img {
        max-width: 100%;
        height: auto;

        [align='right'] {
            display: inline-block;
            margin: auto;
        }
    }

    p {
        @extend %page-text;

        & + h3 {
            padding-top: 47px;
        }
    }

    hr {
        height: 1px;
        border: none;
        color: #aaa;
        background: #aaa;
        margin: 10px 0;
        clear: both;
    }

    %ul {
        ul, ol {
        }
    }

    %li {
        @extend %li-bullet;
        @extend %page-text;
        position: relative;
        padding-left: 28px;

        &:before {
            top: 11px;
            left: 0;
            position: absolute;
        }
    }

    ul {

        @extend %ul;

        li {
            @extend %li;
        }
    }

    ol {
        @extend %ul;
        counter-reset: point;

        > li {
            @extend %li;
            
            position: relative;
            counter-increment: point;

            &:before {
                content: counter(point)'. ';
            }
        }
    }

    dl {
        margin: 0 0 15px 0;
    }

    dt {
        font-weight: bold;
        margin: 0 0 2px;
    }

    dd {
        margin: 0 0 2px;
    }

    table {
        width: 100%;
        text-align: left;
    }

    thead {
        th, td {

        }
    }

    tbody {
        td {

        }

        tr {
            &:nth-child(2n) {
                background-color: #e5f2ed;
            }

            &:hover {
                background-color: #d8e5e0;
            }
        }
    }

    th, td {
        border: 1px solid #c3e2d6;
        padding: 3px 8px;
    }

    tr {

    }

    blockquote {
        background: #f9f9f9;
        quotes: "\201C" "\201D" "\2018" "\2019";
        padding: 20px;
        margin: 0 0 20px;

        &:before,
        &:after {
            color: #000;
            font-size: 40px;
        }

        &:before {
            content: open-quote;
        }

        &:after {
            content: close-quote;
        }

        p {
            display: inline;
        }
    }

    aside {
        margin: 0 0 30px;

        @include breakpoint(tablet) {
            margin: 0;
            display: inline-block;
            vertical-align: top;
            width: 50%;
        }

        &:nth-child(2) {
            @include breakpoint(tablet) {
                padding: 0 0 0 rem(41px, 115px);
            }
        }
    }

    [align='center'] {
        text-align: center;
    }

    [align='right'] {
        text-align: right;
    }
}

.middle-text .fd__title_type1,
.fd__title_type1 {
    @include breakpoint(smalltill){
        font-size: 44px;
    }
    span:last-child,
    span:first-child {
        line-height: normal;
    }
}

.middle-text .fd__title_type2,
.fd__title_type2 {
    @include breakpoint(smalltill){
        font-size: 42px;
    }
}

.middle-text .fd__title_type3,
.fd__title_type3 {
    @include breakpoint(smalltill){
        font-size: 40px;
    }
}