
.choose__reason {
    @extend %inner-layout;

    position: relative;
    text-align: center;
    overflow: hidden;
    background: $color-light-background;
    padding-top: rem(151px);
    padding-bottom: rem(29px + 142px);

    @include breakpoint(desktop) {
        text-align: left;
    }

    .layout {
        min-height: rem(649px);
        margin-bottom: rem(69px);
    }

    &:after {
        content: '';
        background-color: #aaafba;
        width: 1px;
        left: 0;
        right: 0;
        position: absolute;
        margin: auto;
        height: rem(142px);
        bottom: rem(79px);
    }
}

.choose__reason_t1 {
    margin: 0 0 35px;

    @include breakpoint(tablet) {
        margin: 0 0 74px;
    }

    @include breakpoint(desktop) {
        text-align: left;
        position: absolute;
        top: 0;
        left: 0;
    }

    > span {
        line-height: 1.39;
        text-transform: uppercase;
        letter-spacing: -1.1px;
        margin: 0 rem(10px) 0 0;
        font-size: rem(36px);

        br {
            display: none;

            @include breakpoint(desktop) {
                display: block;
            }
        }

        &:first-child {
            color: $color-light-light;
            font-weight: 300;
        }

        &:last-child {
            color: $color-light-header;
            font-weight: 700;
        }

    }
}

.choose__reason_t2 {
    position: relative;
    margin: 0 rem(-12px) rem(62px);

    @include breakpoint(tablet) {
        padding: 0 0 0 rem(101px);
        margin: 0 0 rem(101px);
        display: inline-block;
    }

    @include breakpoint(desktop) {
        padding: 0;
        width: auto;
        position: absolute;
        left: 50%;
        bottom: rem(170px);
    }

    input {
        display: none;

        @for $i from 1 through 10 {

            &:checked {
                &:nth-child(#{$i}) {
                    & ~ ul {
                        li {

                            &:nth-child(#{$i}) {
                                visibility: visible;
                                position: relative;
                                z-index: 1;
                                opacity: 1;
                                
                                @include breakpoint(tablet) {
                                    animation: spaceInUp 1.0s;
                                }
                            }

                        }
                    }
                }
            }

        }
    }

    ul {
        position: relative;
        white-space: nowrap;
    }

    li {
        display: inline-block;
        vertical-align: top;
        white-space: normal;
        width: 100%;
        text-align: left;
        position: absolute;
        z-index: -1;
        visibility: hidden;
        opacity: 0;
        
        @include breakpoint(tablet) {
            z-index: auto;
            opacity: 1;
            visibility: visible;
            will-change: opacity, transform;
            animation: rotateDown 1.0s;
            animation-fill-mode: forwards;
        }

        > div {
            display: inline-block;
            vertical-align: top;

            > span {
                @extend %align-middle;

                text-align: left;
                position: relative;
                color: $color-light-header;
                text-transform: uppercase;
                display: inline-block;
                vertical-align: middle;
                letter-spacing: rem(-0.8px);
                width: rem(599px);
                height: rem(235px);

                @include breakpoint(desktop) {
                    position: absolute;
                    left: rem(85px);
                }

                &:before {
                    background: url(../img/ico/frame.png) no-repeat;
                    background-size: contain;
                    width: 100%;
                    height: 100%;
                    content: '';
                    position: absolute;
                    top: 0;
                    right: 0;
                }

                > span {
                    font-weight: 700;
                    display: inline-block;
                    vertical-align: middle;
                    padding: rem(15px) 0 0 rem(55px);
                    width: 100%;

                    > span {
                        font-size: rem(36px);
                        display: inline-block;
                        vertical-align: bottom;
                        width: calc(100% - #{rem(145px)});
                        margin: 0 0 32px;

                        @include breakpoint(tablet) {
                            margin: 0 0 rem(60px);
                        }
                    }

                    &:before {
                        content: attr(data-step);
                        color: #efefef;
                        font-weight: 700;
                        text-transform: uppercase;
                        display: inline-block;
                        vertical-align: middle;
                        font-size: rem(150px);
                        margin-right: rem(45px);
                    }
                }

            }

            > div {
                position: relative;
                z-index: 1;
                background-color: $color-dark-background;
                text-align: left;
                padding: rem(35px) 12px rem(70px) 12px;
                margin: -52px 0 0;

                @include breakpoint(tablet) {
                    width: rem(634px);
                    top: auto;
                    right: auto;
                    padding: rem(35px) rem(49px) rem(45px);
                    margin: rem(-105px) 0 0 rem(-105px);
                }

                @include breakpoint(desktop) {
                    top: rem(128px);
                    //right: rem(85px);
                    margin: 0;
                }

                p {
                    @extend %page-text;
                    color: $color-dark-text;
                    font-family: $font-verdana;
                    font-weight: 400;
                    line-height: 1.47;

                    &.white {
                        color: #f1f0f0;
                    }
                }
            }
        }


    }

}

.choose__reason_t3 {

    @include breakpoint(desktop) {
        position: absolute;
        bottom: 0;
        left: 0;
    }

    > span {
        line-height: 1.67;
        text-transform: uppercase;
        font-size: 12px;

        &:first-child {
            color: $color-light-light;
            font-weight: 300;
        }

        &:last-child {
            color: $color-light-header;
            font-weight: 700;
        }

        @include breakpoint(tablet) {
            font-size: rem(18px);
        }
    }
}

.choose__reason-button-wrap {
    position: absolute;
    background: #1e1e1e;
    display: flex;
    justify-content: space-between;
    align-items: center;
    outline: 6px solid #fff;
    bottom: -4px;
    right: -11px;
    //width: 50px;
    //height: 22px;
    //padding: 0 9px;
    cursor: pointer;

    width: 85px;
    height: 39px;
    padding: 0 22px 0 18px;

    > span {
        @extend %page-text;
        display: inline-block;
        vertical-align: middle;

        color: $color-dark-text;
        font-weight: 400;
        line-height: 1.47;
    }

    @include breakpoint(tablet) {
        bottom: rem(-8px);
        right: rem(-22px);
        //width: rem(101px);
        //height: rem(45px);
        padding: 0 rem(18px);
    }
}

.choose__reason-next {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    width: rem(23px);

    &:before {
        background: url(../img/ico/next-arrow.png) no-repeat center center;
        background-size: contain;
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        width: rem(23px);
        height: rem(15px);
    }
}
