.responsive-menu {
    font-size: 18px;
    color: #000;
    text-align: center;

    padding: rem(30px) 0;
}

.responsive-mlist {
    width: 100%;
    text-align: center;
}

.responsive-mitem {
    width: 50px;
    height: 50px;
    cursor: pointer;
    vertical-align: middle;
    position: relative;
    margin: 0 rem(10px);
    display: none;

    &:before {
        margin: 0 10px;
        position: absolute;
        left: 50%;
        top: 50%;
        width: 50px;
        height: 50px;
        margin-left: -25px;
        margin-top: -25px;
        transition: width $gEasing 0.2s, height $gEasing 0.2s, margin $gEasing 0.2s;
        transform: translate3d(0,0,0);
        transition-delay: 0.1s;
        backface-visibility: hidden;

        @include breakpoint(tablet){
            content: normal;
            width: rem(50px);
            height: rem(50px);
            margin-left: rem(-25px);
            margin-top: rem(-25px);
            display: block;
            background-position: center center;

        }
    }

    &:hover {
        &:before {
            $size: 60px;
            width: $size;
            height: $size;
            margin-left: -$size * 0.5;
            margin-top: -$size * 0.5;

            @include breakpoint(tablet) {
                $size: 60px;
                width: rem($size);
                height: rem($size);
                margin-left: rem(-$size * 0.5);
                margin-top: rem(-$size * 0.5);
            }

        }
    }

    &.mob {
        @include breakpoint(small){
            display: inline-block;
            &:before {
                content: "";
                background-image: url('../img/views/icon-mob.png');
                background-size: contain;
            }
        }
    }
    &.vtab {
        @include breakpoint(small) {
            display: inline-block;
            &:before {
                content: "";
                background-image: url('../img/views/icon-vtab.png');
                background-size: contain;
            }
        }
    }
    &.tab {
        @include breakpoint(tablet){
            display: inline-block;
            &:before {
                content: "";
                background-image: url('../img/views/icon-tab.png');
                background-size: contain;
            }
        }
    }

    &.lap {
        @include breakpoint(desktop) {
            display: inline-block;
            &:before {
                content: "";
                background-image: url('../img/views/icon-lap.png');
                background-size: contain;
            }
        }

    }

    &.desk {
        @include breakpoint(xdesktop) {
            display: inline-block;
            &:before {
                content: "";
                background-image: url('../img/views/icon-desk.png');
                background-size: contain;
            }
        }

    }

    @include breakpoint(tablet){
        width: rem(50px);
        height: rem(50px);
    }

}

