.warranty {
    position: relative;
    padding-top: rem(276px);
    padding-bottom: rem(155px);
    overflow: hidden;

    .layout {
        max-width: none;
        text-align: center;


        @include breakpoint(tablet) {
            padding-left: rem(100px);
            padding-right: rem(100px);
            display: flex;
            justify-content: space-between;
        }

        @include breakpoint(desktop) {
            padding-left: rem(200px);
            padding-right: rem(200px);
        }

        @include breakpoint(large) {
            padding-left: rem(240px);
            padding-right: rem(240px);
        }
    }

    h2 {
        margin-bottom: rem(20px);
        letter-spacing: rem(-1px);
        min-height: rem(80px);
        box-sizing: content-box;
    }

    &.light li {
        max-width: none;
    }


}

.warranty-item {
    text-align: left;
    //width: rem(556px);
    display: block;
    width: auto;


    ul {
        margin-left: rem(5px);
    }

    @include breakpoint(tablet) {
        width: 39.1%;
        display: inline-block;
        vertical-align: top;

        &:first-child {
            //width: rem(620px);
            width: 43.05%;
        }
    }
}

.warranty__background-text {
    z-index: 0;

    white-space: nowrap;
    position: absolute;
    top: 0;
    bottom: 0;
    left: -50%;
    right: -50%;
    margin: auto;
    text-align: center;
    padding: rem(120px) 0 0 0;

    span {
        color: #efefef;
        font-weight: 700;
        text-transform: uppercase;
        position: relative;
        display: inline-block;
        vertical-align: top;
        font-size: rem(165px);
        letter-spacing: rem(-15px);

        &:after {
            content: '';
            position: absolute;
            top: 0;
            bottom: 0;
            left: rem(-50px);
            right: rem(-50px);
            background: linear-gradient(180deg, rgba(255, 255, 255, 0), #fff);
        }

        @include breakpoint(tablet) {
            font-size: rem(180px);
            letter-spacing: rem(-15px);
        }

        @include breakpoint(desktop) {
            font-size: rem(232px);
            letter-spacing: rem(-22px);
        }

        @include breakpoint(large) {
            font-size: rem(280px);
            letter-spacing: rem(-27px);
        }
    }
}