
%footer-address {
    @extend %small-text;
    display: block;

    color: $color-dark-light;
    font-weight: 400;
    line-height: 1.69;

    @include breakpoint(desktop) {

    }

    @include breakpoint(large) {

    }
}

.footer {
    position: relative;
    background: url(../img/bg/footer-bg-mobile.jpg) center top no-repeat;
    background-size: cover;
    text-align: center;
    padding: 0 0 48px;

    @include breakpoint(tablet) {
        padding-bottom: 0;
    }

    @include breakpoint(desktop) {
        background-image: url(../img/bg/footer-bg.jpg);
    }

    &:before,
    &:after {
        @include breakpoint(desktop) {
            content: '';
            height: 1px;
            position: absolute;
            background: #1e1e1e;
            width: 448px / 1920px * 100%;
            bottom: 129px;
        }

        @include breakpoint(large) {
            bottom: 147px;
        }
    }

    &:before {
        left: 0;

        @include breakpoint(large) {
            content: none;
        }
    }

    &:after {
        right: 0;
    }

    address {
        font-style: normal;
        max-width: 227px;
        margin: 0 auto;

        @include breakpoint(tablet) {
            max-width: none;
            text-align: left;
            position: absolute;
            bottom: 54px;
            left: 22px;
            right: 49px;

            display: flex;
            justify-content: space-between;
            align-items: flex-end;
        }

        @include breakpoint(desktop) {
            bottom: 228px;
            left: 45px;
            right: 45px;
            display: block;
        }

        @include breakpoint(large) {
            bottom: 53px;
            left: 66px;
            right: auto;
            max-width: 400px;
        }

        > span {

            > span {
                @extend %footer-address;
            }

            &:first-child {
                @extend %footer-address;
                margin: 0 0 17px;

                @include breakpoint(tablet) {
                    margin-bottom: 0;
                }

                @include breakpoint(desktop) {
                    left: 0;
                    margin-left: 0;
                }

                @include breakpoint(large) {
                    margin-bottom: 26px;
                }

            }

            &:last-child {

                @include breakpoint(tablet) {

                }

                @include breakpoint(desktop) {
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    margin-left: 431px / 1179px * 100%;
                }

                @include breakpoint(large) {
                    left: auto;
                    bottom: auto;
                    margin-left: 0;
                    position: static;
                }

                > a {
                    @extend %footer-address;
                }
            }
        }

    }
}

.footer-logo {
    text-align: center;
    display: inline-block;
    width: 293px;
    margin: 0 0 50px;

    @include breakpoint(tablet) {
        width: 487px;
        margin: 0 0 157px;
    }

    @include breakpoint(desktop) {
        margin: 0 0 97px;
    }

    @include breakpoint(large) {
        width: auto;
        margin: 0 0 108px;
    }

    img {
        display: inline-block;
    }
}

.footer__top {
    border-bottom: 1px solid #1e1e1e;
    padding: 28px 0;
    margin: 0 0 43px;

    @include breakpoint(desktop) {
        padding: 0;
        height: 80px;
        margin: 0 0 80px;
    }

    @include breakpoint(desktop) {
    }

    @include breakpoint(large) {
        height: 96px;
        margin: 0 0 97px;
    }

    .layout {
        @extend %align-middle;
        height: 100%;
    }

    ul {
        text-align: center;
        display: inline-block;
        vertical-align: middle;
    }

    li {
        margin: 0 0 20px;

        &:last-child {
            margin-bottom: 0;
        }

        a {
            @extend %small-text;

            color: $color-dark-text;
            font-weight: 400;
            line-height: 1.85;
            text-transform: uppercase;

            &:hover {
                color: #fff;
            }
        }

        @include breakpoint(desktop) {
            display: inline-block;
            vertical-align: middle;
            margin: 0 43px;
        }
    }
}

.footer__text {
    @extend %page-text;
    color: $color-dark-text;
    font-weight: 400;
    text-align: center;
    max-width: 574px;
    margin: auto;
    white-space: normal;
    padding: 0 12px;

    br {
        display: none;

        @include breakpoint(tablet) {
            display: block;
        }
    }

    /*@include breakpoint(smalltill){
        font-size: 11px;
        line-height: 15px;
    }*/
}