%second-header {
    @extend %page-second-header;
    text-align: center;
    position: relative;
    padding-left: 12px;
    padding-right: 12px;
    padding-bottom: rem(55px);
    font-weight: 700;
    line-height: 1.39;

    @include breakpoint(tablet) {
        padding-left: rem(20px);
        padding-right: rem(20px);
    }

    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        width: 200px;
        height: 1px;
    }

    span {

        &:first-child {
            font-weight: 300;
            display: block;
        }

    }
}

%second-header-light {
    @extend %second-header;
    color: $color-light-header;

    &:after {
        background: #888d9d;
    }

    span {

        &:first-child {
            color: $color-light-light;
        }

        //&:last-child {

        //}

    }
}

%second-header-dark {
    @extend %second-header;
    color: $color-dark-header;
    font-weight: 300;

    &:after {
        background-color: $color-input-background;
    }
}

%third-header {
    @extend %page-third-header;
    text-align: center;
    padding-left: 12px;
    padding-right: 12px;

    @include breakpoint(tablet) {
        padding-left: rem(20px);
        padding-right: rem(20px);
    }
}

%third-header-white {
    @extend %third-header;
    color: $color-light-light;
    font-weight: 300;
}

%third-header-dark {
    @extend %third-header;
    color: $color-dark-light;
    font-weight: 300;
}

.background-text {
    font-family: $font-verdana;
    position: absolute;
    text-transform: uppercase;
    left: 0;
    right: 0;
    opacity: 0.7;
    color: rgba($color-dark-bold, 0.02);
    font-size: vw(520px);
    text-align: center;
    font-weight: 700;
    line-height: 1;
    bottom: vw(-95px);
}

[data-fit='cover'] {
    position: absolute;
    width: 100%;
    max-width: none;

    @supports (object-fit: cover) {
        height: 100%;
        object-fit: cover;
        will-change: width, height;
    }
}

.middle-text {
    display: inline-block;
    vertical-align: top;
    position: relative;
    z-index: 1;
    width: 100%;
    white-space: normal;
    padding: vh(158px, nth($v-height, 4)) 0 0 0;

    @include breakpoint(tablet) {
        padding: vh(123px, nth($v-height, 3)) 0 0 0;
    }

    /*@include breakpoint(desktop) {
        padding: vh(175px, nth($v-height, 2)) 0 0 0;
    }*/

    @include breakpoint(large) {
        padding: vh(254px, nth($v-height, 1)) 0 0 0;
    }

    h1 {
        @extend %page-header;
        position: relative;
        color: $color-dark-bold;

        > span {
            display: block;

            &:first-child {
                position: absolute;
                bottom: 100%;
                margin: 0 0 rem(12px) 0;
                left: 0;
                right: 0;
            }

            &:first-child,
            &:nth-child(3) {
                font-size: 0;

                span {
                    color: $color-dark-bold;
                    //font-weight: 500;
                    display: inline-block;
                    letter-spacing: -0.6px;
                    margin: 0 rem(20px) 0 0;
                    font-size: rem(32px);

                    &:first-child {
                        color: rgba($color-dark-header, 0.25);
                        font-weight: 400;
                        letter-spacing: rem(-1.4px);
                    }

                }
            }

            &:nth-child(2) {
                display: block;
                margin-bottom: rem(24px);
            }

            &:last-child {
                margin-bottom: 0;
                span {
                    display: block;
                    letter-spacing: -0.9px;
                    font-size: rem(40px);
                }
            }

        }
    }

    p {
        @extend %page-text;

        br {
            display: none;
            
            @include breakpoint(tablet) {
                display: block;
            }
        }

        @include breakpoint(tablet) {
            max-width: rem(560px);
        }

        padding-left: rem(12px);
        padding-right: rem(12px);
        margin: 0 auto rem(65px);
    }

    &.response {
        //opacity: 0;
        transition: opacity 0.3s;

        .action-form {
            display: none;
        }

        h2 {
            display: none;
        }

        .form-answer {
            position: relative;
            visibility: visible;
            opacity: 1;
        }
    }
}
.index {
    .middle-text {
        .fd__index-title {
            span {
                @extend %page-third-header;
                text-transform: none;
                font-family: $font;
                font-weight: 300;
            }

        }
    }
}

.flex-lt {
    display: flex;
    align-items: center;
    text-align: center;

    & > div {
        width: 100%;
    }
}

.middle-flex {
    height: 100%;
    //background: rgba(255,255,255,0.2);
    margin: 86px 0 0 0 !important;
    padding: 0 !important;

    @include breakpoint(desktop) {
        margin-top: rem(130px) !important;
    }
}

.top-flex {
    display: none;
    height: 10%;
    margin-top: 86px;
    background: rgba(255,255,255,0.1);

    & > div {
        position: relative;
        height: 100%;
        width: 1005;
    }

    span {
        z-index: 1;
        width: 1px;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 50%;
        height: auto;
        background: #ffffff;
        margin: 0;
        transition: opacity ease-out 1s, bottom ease-out 1s, top ease-out 1s, height ease-out 1s;
        transition-delay: 0.5s;
    }
}

.form-answer {
    @extend %align-middle;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    padding: rem(20px);
    opacity: 0;
    visibility: hidden;
    transition: visiblity 0.5s, opacity 0.5s;
    white-space: nowrap;

    span {
        /*@extend %page-third-header;

        white-space: normal;
        color: $color-light-header;
        font-weight: 500;
        line-height: 2;*/
        white-space: normal;
        @extend %page-third-header;
        color: $color-dark-light;
        font-weight: 300;
        line-height: 2;
        max-width: 784px;
        margin: auto;
        display: inline-block;
    }
}

.ru-lt-1 {
    margin: 0 rem(-2px) 0 0;
}

.ru-lt-2 {
    margin: 0 rem(-2px) 0 0;
}

.ru-lt-3 {
    margin: 0 rem(4px) 0 0;
}

.ru-lt-4 {
    margin: 0 rem(8px) 0 0;
}

.ru-lt-5 {
    margin: 0 rem(5px) 0 0;
}

.ru-lt-6 {
    margin: 0 rem(-1px) 0 0;
}

.ru-lt-7 {
}

.ru-lt-8 {
}

.direct-line {
    width: 1px;
    opacity: 0.2;
    background-color: $color-dark-bold;
    z-index: 1;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;

    height: vh(40px, nth($v-height, 4));
    bottom: vh(80px, nth($v-height, 4));

    &.hide {
        @include breakpoint((desktop, large)){
            display: none;
        }
    }

    &.hide-mob {
        @include breakpoint(smalltill){
            display: none;
        }
    }

    @include breakpoint(landscape) {
        @media (max-height: 320px) {
            display: none;
        }
    }

    @include breakpoint(tablet) {
        height: vh(56px, nth($v-height, 3));
        bottom: vh(90px, nth($v-height, 3));

        @media (max-height: 490px) {
            display: none;
        }
    }

    @include breakpoint(desktop) {
        bottom: vh(100px, nth($v-height, 2));

        @media (max-height: 540px) {
            display: none;
        }
    }

    @include breakpoint(large) {
        height: vh(85px, nth($v-height, 1));
        bottom: vh(166px, nth($v-height, 1));

        @media (max-height: 610px) {
            display: none;
        }
    }

    @include breakpoint(portrait) {
        display: block;
    }
    
}

.arrow {
    position: absolute;
    cursor: pointer;
    opacity: 0.6;
    z-index: 1;
    transition: opacity 0.3s;
    left: 0;
    right: 0;
    margin: auto;
    background: url(../img/ico/arrow.png) center center no-repeat;
    background-size: contain;
    width: 48px;
    height: 48px;

    bottom: vh(10px, nth($v-height, 4));

    @include breakpoint(tablet) {
        bottom: vh(25px, nth($v-height, 3));
        width: 40px;
        height: 40px;
    }

    @include breakpoint(desktop) {
        //bottom: 25px;
        bottom: vh(25px, nth($v-height, 2));
        width: 48px;
        height: 48px;
    }

    @include breakpoint(large) {
        bottom: vh(50px, nth($v-height, 1));
        width: 64px;
        height: 63px;
    }

    &:hover {
        opacity: 1;
    }
}

.iScrollHorizontalScrollbar {
    position: absolute;
    z-index: 9999;
    height: 10px;
    bottom: 0;
    overflow: hidden;
    border: 1px solid rgba(255,255,255, 0.26);
    border-radius: round((6px + 2px) * 0.5);

    left: 40%;
    right: 40%;

    @include breakpoint(desktop) {
        left: 45%;
        right: 45%;
    }
}

.iScrollIndicator {
    width: 50%;
    position: absolute;
    height: 100%;
    background: rgba(255, 255, 255, 0.26);
    border-radius: round((6px + 2px) * 0.5);
    overflow: hidden;
}

.button-wrap {
    display: inline-block;
    vertical-align: middle;
    position: relative;
}

.button {
    @extend %button;

    &.light {
        border-color: $color-light-light;

        span {
            color: $color-light-header;
        }

        &:hover {

            @include breakpoint(desktop) {
                background-color: $color-light-header;
                border-color: $color-light-header;

                span {
                    color: $color-dark-bold;
                }
            }
        }
    }

    &.dark {
        border-color: $color-dark-header;

        span {
            color: $color-dark-bold;
        }

        &:hover {

            @include breakpoint(desktop) {
                background-color: #ffffff;
                border-color: #ffffff;

                span {
                    color: $color-light-header;
                }
            }
            
        }
    }
}

.align-center {
    text-align: center;
}

.image-frame {
    max-width: rem(900px);
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 0 rem(40px) rgba(0, 0, 0, 0.4);
    border-radius: rem(15px) rem(15px) 0 0;

    //&:before {
    //    content: '';
    //    position: relative;
    //    background: url(../img/bg/frame-head.png) no-repeat top center;
    //    background-size: contain;
    //    width: 100%;
    //    height: 79px;
    //    display: block;
    //}

    > div {
        @extend %align-middle;
        height: rem(82px);
        background-color: #e4e9ef;
        padding: 0 rem(22px) 0 0;

        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.frame-controls {
    display: inline-block;
    vertical-align: middle;
    width: rem(125px);
    text-align: center;

    span {
        display: inline-block;
        vertical-align: middle;
        width: rem(15px);
        height: rem(15px);
        margin: 0 3px;
        border-radius: 100%;

        &:nth-child(1) {
            background-color: #ef634c;
        }

        &:nth-child(2) {
            background-color: #f9bc5f;
        }

        &:nth-child(3) {
            background-color: #44c96e;
        }
    }
}

.frame-input {
    display: inline-block;
    vertical-align: middle;
    width: rem(529px);
    height: rem(36px);
    margin: 0 rem(15px) 0 0;
    border-radius: 2px;
    position: relative;
    background-color: #ffffff;
    color: #bebfc1;
    padding: 0 rem(12px);
    font-size: rem(13px);
    line-height: rem(36px);

    &:after {
        width: rem(23px);
        height: rem(23px);
        background: url(../img/ico/browser-controls-refresh.png);
        background-size: contain;
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        right: rem(7px);
        z-index: 2;
    }

}

.frame-face-input {
    display: inline-block;
    vertical-align: middle;
    border-radius: 2px;
    width: rem(210px);
    height: rem(36px);
    background-color: #ffffff;
    position: relative;

    &:after {
        background: url(../img/ico/browser-controls-search.png);
        background-size: contain;
        width: rem(29px);
        height: rem(23px);
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        right: rem(7px);
        z-index: 2;
    }
}

h1.fd__index-title {
    font-size: 0;
    position: relative;
    color: $color-dark-bold;
    font-weight: 300;
    line-height: 1.67;
    letter-spacing: 0;
    text-transform: none;
    transform: translate3d(0, -20px, 0);
    opacity: 0;
    transition: opacity 1s ease-out, transform 0.8s linear;
    backface-visibility: hidden;
    //margin-bottom: rem(112px);
    //margin-bottom: rem(70px);
    margin-bottom: 8vh;

    span {
        &:first-child {
            @extend %page-third-header;
            font-family: $font;
            text-transform: none;
            font-weight: 300;
            display: block;
            //font-size: 12px;
            //margin-bottom: rem(57px);
            margin-bottom: rem(47px);

/*             @include breakpoint(tablet) {
                font-size: 18px;
            }

            @include breakpoint(desktop){
                font-size: 24px;

            }; */

            @include breakpoint(small){
                //font-size: rem(24px);
            }
            @include breakpoint(tablet) {
                margin-bottom: 2.5vh;
            }
            @include breakpoint(desktop) {
                //margin-bottom: rem(57px);
            }
            @include breakpoint(large) {
                margin-bottom: 5.3vh;

                @media (max-height: 800px) {
                    margin-bottom: 4vh;
                }
            }
        }
        &:nth-child(2) {
            display: block;
            height: vw(153px, 916/ 100 * 1px);
            max-height: 153px;
            position: relative;

            > div {
                position: absolute;
                left: 0;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                height: vw(257px, 916/ 100 * 1px);
                max-height: 257px;
                opacity: 0;
                transition: opacity 1s ease-out;

                &.visible {
                    opacity: 1;
                }

                @include breakpoint(tablet) {
                    height: vw(130px, 916/ 100 * 1px);
                    max-height: 130px;
                }

                @include breakpoint(large) {
                    height: vw(257px, 916/ 100 * 1px);
                    max-height: 257px;
                }
            }

            &:nth-child(2) {
                margin-bottom: 0;
            }

            img {
                width: auto;
                max-width: none;
                height: 100%;
                visibility: hidden;
                display: none;
            }
        }
    }

    @include breakpoint(tablet) {
        margin-bottom: 6.5vh;
    }
    @include breakpoint(large) {
        margin-bottom: 12vh;
        @media (max-height: 800px) {
            margin-bottom: 7vh;
        }
    }

}

.ic-img {
    position: relative;
}

.intro-wrap {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 5%;
    right: 5%;

    @include breakpoint(tablet) {
        left: 15%;
        right: 15%;
    }

    @include breakpoint(large) {
