    }
}

[data-stroke=true] {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
}

.social-network {
    $list: fb, tw, be, ball, pin, in, b;
    $list-var: $fb, $tw, $be, $ball, $pin, $in, $b;

    li {
        display: inline-block;
        vertical-align: middle;
        position: relative;
        margin: 0 rem(16px);


        a {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
        }

        &:before {
            content: '';
            display: block;
            
            @extend .icon;
        }

        $i: 1;
        @each $item in $list {
            &.#{$item} {
                &:before {
                    @include sprite(nth($list-var, $i));
                }
            }

            $i: $i + 1;
        }
    }
}

.input {
    @extend %input;
}

.input-wrap {
    margin-bottom: 20.5px;
    width: 100%;

    @include breakpoint(desktop) {
        margin-bottom: 0;
        margin: 0 rem(5px);
    }

    &.text {
        width: 100%;
    }

    label {
        @extend %page-text;
        color: $color-dark-light;
        display: block;
        padding: 0 0 0 rem(24px);
        margin: 0 0 rem(14px);
    }

    textarea {
        @extend %input;

        line-height: 1.69;
        width: 100%;
        height: rem(155px);
        padding-top: rem(22px);
    }

}

.row {
    margin: 0 0 rem(41px);
    @include breakpoint(desktop) {
        display: flex;
        justify-content: space-between;
    }

}

.action-form {
    padding: 0 rem(14px);
    text-align: left;

    &:before {
        opacity: 0;
        visibility: hidden;
        transition: opacity $gEasing $gDuration, visibility $gEasing $gDuration;
    }

    &.loading {
        position: relative;
        opacity: 0.3;
        &:before {
            content: "";
            position: absolute;
            opacity: 1;
            visibility: visible;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            cursor: wait;
            background: url('../img/preloader.gif') center no-repeat;
        }
    }

    @include breakpoint(desktop) {
        max-width: 857px + 50px;
        padding: 0 rem(50px);
        margin: auto;
    }

    .button {
        min-width: rem(284px);
    }
}