
.approach {
    background: $color-light-background;
    overflow: hidden;

    .layout {
        > h2 {
            @extend %second-header-light;

            &:first-child {
                padding-top: rem(150px);
            }
            
            span {
                margin: 0 0 rem(10px);
            }

        }

        > h3 {
            @extend %third-header-white;
        }
    }


}

.approach__list-wrap {
    position: relative;
    padding: rem(88px) 0 0 0;
}

.approach__list {
    $right-border: 120px / 1920px * 100%;
    counter-reset: point;
    padding: 0 41px 28px;

    @include breakpoint(tablet) {
        padding: 0 37px 49px 34px;
    }

    @include breakpoint(desktop) {
        padding: 0 60px 26px 68px;
    }

    @include breakpoint(large) {
        padding: 0 150px / 1920px * 100% 26px 347px / 1920px * 100%;
    }

    &:first-child {
        position: relative;

        @include breakpoint(tablet) {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
        }

        @include breakpoint(desktop) {
            flex-wrap: nowrap;
            justify-content: space-between;
        }

        .approach__item {
            counter-increment: point 1;

            &:nth-child(2) {
                @include breakpoint(tablet) {
                    padding-right: 0;
                    padding-left: 34px;
                }

                @include breakpoint(desktop) {
                    padding-left: 0;
                }
            }

            &:last-child {
                @include breakpoint(tablet) {
                    padding-right: 0;
                }
            }

            @include breakpoint(tablet) {
                width: 50%;
                display: inline-block;
                vertical-align: top;
                padding-right: 20px;
            }
            
            @include breakpoint(desktop) {
                width: 100 / 3 * 1%;
                max-width: 390px;
            }

        }

        &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            background: #f9f9f9;
            height: 6px;
            right: 0;
            
            @include breakpoint(desktop) {
                right: $right-border;
            }
        }
    }

    &:last-child {
        counter-reset: point 3;
        position: relative;
        padding-top: 37px;
        padding-bottom: 34px;
        margin-bottom: 68px;

        @include breakpoint(tablet) {
            padding: 0;
            margin-bottom: 0;
            height: 650px;
        }

        @include breakpoint(desktop) {
            height: 798px;
        }

        &:before {
            content: '';
            position: absolute;
            background: #efefef;
            top: 37px;
            bottom: 0;
            width: 9px;
            right: 12px;

            @include breakpoint(tablet) {
                top: 0;
                right: 0;
                width: 10px;
                background-color: #f9f9f9;
            }

            @include breakpoint(desktop) {
                right: $right-border;
            }
        }

        &:after {
            content: '';
            right: 12px;
            bottom: 0;
            background: #efefef;
            width: 65px;
            position: absolute;
            height: 9px;

            @include breakpoint(desktop) {
                background: url(../img/ico/approach-logo.png) no-repeat right bottom;
                background-size: contain;
                bottom: 0;
                right: 0;
                width: rem(101px);
                height: rem(103px);

                @include breakpoint(desktop) {
                    right: $right-border;
                }

            }
            
        }

        .layout {
            position: static;

            &:before {
                @extend .icon;
                @include sprite($arrow-left);

                content: '';
                position: absolute;
                top: 30px;
                right: 21px;

                @include breakpoint(tablet) {
                    @include sprite($approach-arrow);
                    
                    bottom: 0;
                    top: 0;
                    left: 0;
                    right: 0;
                    margin: auto;
                }

            }
        }

        .approach__item {
            counter-increment: point 1;
            position: relative;

            &:after {
                content: '';
                @extend .icon;
                @include sprite($arrow-down);
                position: absolute;
                left: -32px;
                bottom: -34px;
                
                @include breakpoint(tablet) {
                    content: none;
                }
            }

            &:last-child {
                &:after {
                    bottom: -73px;
                    @include sprite($arrow-angle-right);
                }
            }
            
            @include breakpoint(tablet) {
                position: absolute;
                left: 50%;
                max-width: 286px / 748px * 100%;
            }

            @include breakpoint(desktop) {
                max-width: 330px / 1920px * 100%;
            }

            &:nth-child(1) {
                
                @include breakpoint(tablet) {
                    top: 49px;
                    margin-left: 64px;
                }

                @include breakpoint(desktop) {
                    top: 64px;
                    margin-left: 233px;
                }


            }

            &:nth-child(2) {
                

                @include breakpoint(tablet) {
                    top: 322px;
                    margin-left: 64px;
                }

                @include breakpoint(desktop) {
                    top: 399px;
                    margin-left: 233px;
                }
            }

            &:nth-child(3) {
                
                @include breakpoint(tablet) {
                    top: 408px;
                    left: 31px;
                }

                @include breakpoint(desktop) {
                    top: 489px;
                    left: 50%;
                    margin-left: -330px / 748px * 100%;
                }

                @include breakpoint(large) {
                    margin-left: -558px;
                }
            }

            &:nth-child(4) {
                

                @include breakpoint(tablet) {
                    top: 135px;
                    left: 31px;
                }

                @include breakpoint(desktop) {
                    top: 164px;
                    left: 50%;
                    margin-left: -330px / 748px * 100%
                }

                @include breakpoint(large) {
                    margin-left: -558px;
                }
            }
        }
    }
}

%approach-list-decoration {
    content: '';
    background-color: #888d9d;
    position: absolute;
    bottom: 0;
    left: 0;
}

.approach__item {
    margin: 0 0 34px;

    @include breakpoint(tablet) {
        margin: 0;
    }

    &:before {
        content: '/\00a0 0'counter(point);
        display: block;
        color: $color-light-light;
        font-family: $font-verdana;
        font-size: 14px;
        font-weight: 400;
        line-height: 1.57;
        margin: 0 0 rem(7px);
    }

    h3 {
        @extend %page-third-header;
        color: $color-light-header;
    }

    p {
        @extend %page-text;
        color: $color-light-text;
        position: relative;
        line-height: 1.47;
        padding: 0 0 rem(33px);

        &:before {
            @extend %approach-list-decoration;
            width: 139px;
            height: 1px;
        }

        &:after {
            @extend %approach-list-decoration;
            width: 49px;
            height: 4px;
            margin: 0 0 -4px;
        }
    }
}
