$width: 68px;
$height: 68px;
$borderWidth: 4px;
$margin: 12px;
.pager {
    text-align: center;
    padding: rem(32px) 0 rem(62px) 0;
}

.pager-item {
    font-weight: bold;
    font-family: $font-verdana;
    color: $color-light-header;
    font-size: 15px;
    line-height: 64px;
    display: inline-block;
    margin: 0 rem($margin);
    position: relative;
    vertical-align: top;

    &.active {
        & > * {
            width: $width;
            height: $height;
            line-height: $height + $borderWidth;
            border-color: transparent;
            background-color: $color-light-header;
            color: $color-link-hover;

            @include breakpoint(tablet) {
                width: rem($width);
                height: rem($height);
                line-height: rem($height + $borderWidth);
            }
        }
    }

    & > a {
        cursor: pointer;
        box-sizing: border-box;
        width: $width;
        height: $height;
        border: $borderWidth solid $color1;
        transition: color $gEasing $gDuration, background-color $gEasing $gDuration, border-color $gEasing $gDuration;

        &:hover {
            border-color: transparent;
            background-color: $color-light-header;
            color: $color-link-hover;
        }

        @include breakpoint(tablet) {
            width: rem($width);
            height: rem($height);
        }
    }

    & > * {
        cursor: default;
        display: block;
        color: $color-light-header;
    }

    &.mob-only {
        display: inline-block;

        &.active {
            display: none;
        }

        @include breakpoint(small){
            display: none;
        }
    }

    &.desk-only {
        display: none;

        @include breakpoint(small){
            display: inline-block;
        }
    }

    @include breakpoint(tablet) {
        font-size: rem(15px);
        line-height: rem($height - $borderWidth);
    }
}


.pager_short {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 10;
    padding: 0;
    bottom: 10px;

    .pager-item {
        position: absolute;
        bottom: 0;
        margin-left: 0;
        margin-right: 0;

        &:first-child {
            left: 10px;
        }
        &:last-child {
            right: 10px;
        }

        @include breakpoint(tablet) {
        }

        @include breakpoint(tablet) {
            &:first-child {
                left: 17%;
                margin-left: -(rem($width * 0.5));
            }
            &:last-child {
                right: 17%;
                margin-right: -(rem($width * 0.5));
            }
        }

    }

    @include breakpoint(tablet) {
        bottom: rem(30px);
    }
}