@import "base/typography";

.article {
    @extend %content;


    .layout {
        @extend %inner-layout;
        margin: auto;

        @include breakpoint(desktop) {
            padding: 0;
            max-width: 976px;
        }
    }

    .button {
        min-width: 190px;

        @include breakpoint(tablet) {
            min-width: rem(277px);
        }
    }

    &.light {
        background-color: $color-light-background;

        h1, h2, h3 {
            color: $color-light-header;
        }

        p, li {
            color: $color-light-light;

            @include breakpoint(tablet) {
                max-width: 490px;
            }

            & + .image-frame,
            & + div img {
                margin-top: rem(20px);
            }
        }
    }

    img {
        
    }

    .align-center {

        p {
            max-width: none;
            margin-bottom: rem(21px);
        }
    }

    .align-center,
    .image-frame {
        margin-bottom: rem(67px);
    }

    p {
        span {
            color: $color-light-header;
            font-weight: 100;
            text-transform: uppercase;
            display: block;
            line-height: 1;
            font-family: $font;
            font-size: rem(120px);

            & + b {
                color: $color-light-header;
            }
        }
    }
}