.why {
    position: relative;
    text-align: center;
    padding-top: rem(38px);
    padding-bottom: rem(127px);

    .layout {
        max-width: none;
        text-align: center;


        @include breakpoint(tablet) {
            padding-left: rem(60px);
            padding-right: rem(60px);
            display: flex;
            justify-content: space-between;
        }

        @include breakpoint(desktop) {
            padding-left: rem(200px);
            padding-right: rem(200px);
        }

        @include breakpoint(large) {
            padding-left: rem(210px);
            padding-right: rem(210px);
        }
    }

    h2 {
        margin-bottom: rem(78px);
        letter-spacing: rem(-1px);
        box-sizing: content-box;
    }



    &.light li {
        max-width: none;
        margin-top: 0;
        font-family: "Acrom", Verdana, Geneva, sans-serif;
        padding-left: 0;
        font-weight: 300;
        overflow: hidden;
        font-size: nth($font-size-h2, 4);
        line-height: 25px;
        letter-spacing: -0.6px;
        margin-bottom: 25px;


        &:before {
            content: normal;

        }
        &:last-child {
            margin-bottom: 0;
        }

        @include breakpoint(tablet) {
            font-size: rem(nth($font-size-h2, 2));
            line-height: rem(41px);
            letter-spacing: -1px;
            height: rem(124px);
            margin-bottom: rem(19px);
        }

        @include breakpoint(large) {
            font-size: rem(nth($font-size-h2, 1));
            line-height: rem(50px);
            letter-spacing: -1.2px;
            height: rem(150px);
            margin-bottom: rem(23px);
        }
    }

    @include breakpoint(tablet) {
        padding-top: rem(75px);
        padding-bottom: rem(255px);
    }

}

.why-item {
    text-align: left;
    //width: rem(556px);
    display: block;
    width: auto;
    text-transform: uppercase;

    ul {
        margin-left: rem(5px);
        @include breakpoint(tablettill){
            margin-bottom: 25px;
        }
    }

    @include breakpoint(tablet) {
        width: 50.1%;
        display: inline-block;
        vertical-align: top;

        &:first-child {
            //width: rem(620px);
            width: 45%;
        }
    }
    @include breakpoint(desktop) {
        width: 50.1%;

        &:first-child {
            //width: rem(620px);
            width: 43%;
        }
    }
    @include breakpoint(large) {
        width: 50.1%;

        &:first-child {
            //width: rem(620px);
            width: 40%;
        }
    }
}

.why__background-text {
    z-index: 0;

    white-space: nowrap;
    position: absolute;
    top: 0;
    bottom: 0;
    left: -50%;
    right: -50%;
    margin: auto;
    text-align: center;
    padding: rem(120px) 0 0 0;

    span {
        color: #efefef;
        font-weight: 700;
        text-transform: uppercase;
        position: relative;
        font-size: 190px;
        display: inline-block;
        vertical-align: top;
        font-size: rem(165px);
        letter-spacing: rem(-15px);

        &:after {
            content: '';
            position: absolute;
            top: 0;
            bottom: 0;
            left: rem(-50px);
            right: rem(-50px);
            background: linear-gradient(180deg, rgba(255, 255, 255, 0), #fff);
        }

        @include breakpoint(tablet) {
            font-size: rem(180px);
            letter-spacing: rem(-15px);
        }

        @include breakpoint(desktop) {
            font-size: rem(232px);
            letter-spacing: rem(-22px);
        }

        @include breakpoint(large) {
            font-size: rem(280px);
            letter-spacing: rem(-27px);
        }
    }
}

.why2 {
    .layout {
        max-width: none;
        display: block;

        p {
            max-width: none;
        }

        @include breakpoint(tablet) {
            margin: 0 rem(50px);
        }
        @include breakpoint(desktop) {
            margin: 0 rem(100px);
        }
        @include breakpoint(large) {
            margin: 0 rem(250px);
        }
    }
    & > h2 {
        @extend %second-header-light;
        margin-bottom: rem(78px);
        letter-spacing: rem(-1px);
        box-sizing: content-box;

        &:after {
            content: normal;
        }

        span {
            margin: 0 0 rem(10px);
        }
    }
    .why-dl {
        text-align: left;
        padding-top: rem(33px);
        @include breakpoint(tablet) {
            padding-top: rem(54px);
        }
        @include breakpoint(large) {
            padding-top: rem(65px);
        }

        /*@include breakpoint(tablet) {
            margin: 0 rem(50px);
        }
        @include breakpoint(desktop) {
            margin: 0 rem(100px);
        }
        @include breakpoint(large) {
            margin: 0 rem(250px);
        }*/
    }
    .why-dl-row {
        margin-bottom: rem(95px);
        overflow: hidden;
    }
    .why-dt {
        text-transform: uppercase;
        font-family: "Acrom", Verdana, Geneva, sans-serif;
        font-weight: 300;
        font-size: nth($font-size-h2, 4);
        line-height: 25px;
        margin-bottom: rem(20px);
        color: $color-light-light;

        @include breakpoint(tablet) {
            font-size: rem(36px);
            line-height: rem(41.5px);
        }

        @include breakpoint(desktop) {
            clear: both;
            width: 48%;
            float: left;
            font-size: nth($font-size-h2, 2);
            margin-bottom: 0;
        }

        @include breakpoint(large) {
            font-size: rem(nth($font-size-h2, 1));
            line-height: rem(50px);
        }
    }

    .why-dd {
        font-size: 11px;
        line-height: 15px;
        box-sizing: content-box;
        clear: right;

        li,
        p {
            color: $color-light-text;
        }

        b {
            color: $color-light-header;
        }

        @include breakpoint(tablet) {
            font-size: rem(15px);
        }

        @include breakpoint(desktop) {
            padding-left: 52.9%;
            //width: 44%;
            width: 47%;
            font-size: rem(15px);
            line-height: rem(22px);

        }
    }
}