.fd__menu_line {
    text-align: center;
    .m-item {
        font-size: 12px;
        &.active {
            cursor: default;

            &:before {
                color: $color-link;
            }

            a {
                color: $color-link-hover;
                cursor: default;
            }
        }

        &:first-child {
            &:before {
                content: normal;
            }
        }

        @include breakpoint(small){
            display: inline-block;

            &:before {
                content: "\\";
                display: inline-block;
                margin: 0 rem(18px);
            }
        }

        @include breakpoint(tablet) {
            font-size: rem(15px);
        }
        @include breakpoint(desktop) {
            font-size: rem(18px);
        }
    }
}