.start-screen-cats-list {
    text-align: center;
    font-size: 0;
    letter-spacing: 0;
    padding-top: rem(43px);

    @include breakpoint(small){
        padding-top: rem(72px);
    }
    @include breakpoint(large){
        //padding-top: rem(95px);
        padding-top: rem(87px);
    }
}

.start-screen-cats-items {
    display: inline-block;
    vertical-align: top;
    margin: 0 rem(10px);
    @extend  %normal-text;

    @include breakpoint(smalltill){
        width: 30%;
    }

    @include breakpoint(small){
        margin: 0 rem(18px);
    }
    @include breakpoint(tablet) {
        width: rem(250px);
    }
}

.start-screen-cats-item-content {
    height: rem(150px);
    opacity: 0.7;
    @extend %small-text;


    & > div {
        margin-bottom: rem(10px);

        &:last-child {
            margin-bottom: 0;
        }
    }

    @include breakpoint(tablet){
        height: rem(100px);
    }

}

.start-screen-cats-item-footer {
    @extend %small-text;
    line-height: normal;
    opacity: 0.7;

    @include breakpoint(smalltill){
        font-size: 8px;
        display: none;
    }
}

.start-screen-cats_double {
    $width: 475px;

    .start-screen-cats-list {
        padding-top: 0;
    }

    .start-screen-cats-items {
        display: none;
        color: color('dark');
        opacity: 0.7;
        width: 40%;
        margin: 0 5%;
        @extend %page-text;

        @include breakpoint(tablet) {
            display: inline-block;
            width: rem(340px);
            margin: 0 rem(17px);

            &:nth-child(2) {
                width: rem(240px);
                margin: 0 rem(117px);
            }
        }
        @include breakpoint(desktop) {
            width: rem(370px);
            margin: 0 rem(17px);

            &:nth-child(2) {
                width: rem(240px);
                margin: 0 rem(145px);
            }
        }

        @include breakpoint(large) {
            //font-size: nth($font-size-sm, 1);
            //line-height: rem(20px);
            padding-top: rem(60px);
        }
    }
}

.start-screen-cats_triple {
    .start-screen-cats-list {
        margin-bottom: rem(50px);
    }
    .start-screen-cats-items {
        width: 30%;
        margin: 0 0.5%;
        @include breakpoint(small){
            width: 25%;
        }
        @include breakpoint(large) {
            width: rem(330px);
            &:nth-child(2) {
                width: rem(340px);
            }
        }
    }
    .start-screen-cats-item-content {
        height: auto;
    }
}

.start-screen-cats_tetra {
    .start-screen-cats-list {
        padding-top: 0;

        @include breakpoint(small){
            margin: 0 5%;
        }

        @include breakpoint(dektop) {
            margin: 0 20%;
        }
    }
    .start-screen-cats-items {
        @extend %small-text;
        color: color('dark');
        opacity: 0.7;
        width: 23%;
        margin: 0 1%;
    }
}

.start-screen-cats_mob-sheight {
    @include breakpoint(smalltill){
        .start-screen-cats-item-content {
            & > div {
                height: 30px;
                margin-bottom: 5px;
            }
        }
    }
}

.start-screen-cats_mob-block {
    @include breakpoint(smalltill){
        .start-screen-cats-items {
            width: 100%;
            margin-bottom: 10px;
        }
        .start-screen-cats-item-content {
            & > div {
                margin-bottom: 0;
            }
        }
    }
}