$popupEasing: ease-out;
$popupDuration: 0.5s;

.fd__popup {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(10, 20, 31, 0.94);
    z-index: 2;
    overflow: auto;
    text-align: center;
    visibility: hidden;
    white-space: nowrap;
    opacity: 0;

    -webkit-overflow-scrolling: touch;

    transition: opacity $popupEasing $popupDuration,
                visibility $popupEasing $popupDuration;

    will-change: opacity, visibility;

    &.visible {
        opacity: 1;
        visibility: visible;

        .pop-btn-close {
            opacity: 1;
        }
    }

    .in {
        .fd__stripe {
            opacity: 1;

            @include breakpoint(tablet) {
                height: auto;
                bottom: rem(-60px);
            }

            
        }

    }
}

.pop-content.in + .pop-btn-close {
    opacity: 1;
}

.pop-wrap {
    @extend %align-middle;
    position: relative;
    text-align: center;
    height: 100%;
    white-space: nowrap;
    padding: rem(20px) 0;
}

.pop-title {
    text-align: center;
    text-transform: uppercase;
}

.popup-menu {
    text-transform: uppercase;
}

.fd__pmenu-cell {

    @include breakpoint(tablet) {
        width: 50%;
    }
    
}

.pm-item {

    &:last-child {
        margin-bottom: 0;
    }

    &.active {
        cursor: default;
    }
}

.fd__stripe {
    $tlEasing: ease-out;
    $tlDuration: 0.5s;

    width: 1px;
    background-color: rgba($color-dark-bold, 0.27);
    opacity: 0;
    margin: 14px auto;
    height: 40px;
   
    transition:
            top $tlEasing $tlDuration,
            bottom $tlEasing $tlDuration,
            opacity $tlEasing $tlDuration,
            height $tlEasing $tlDuration,
            margin $tlEasing $tlDuration;

    will-change: top, bottom, opacity, height, margin;

    @include breakpoint(tablet) {
        position: absolute;
        top: rem(-60px);
        bottom: 150%;
        left: 468 / 1400 * 100%;
        height: auto;
        margin: 0;
    }

}

.fd__pmenu {
    width: 100%;
    text-align: center;
    display: inline-block;
    position: relative;
    white-space: normal;
    vertical-align: middle;
    overflow: hidden;
    padding: rem(50px) 0;
}

.fd__pmenu-logo {
    width: rem(76px);
    height: rem(76px);
    margin: 0 auto 23px;

    @include breakpoint(tablet) {
        margin-bottom: rem(73px);
    }

    img {
        width: 100%;
        height: auto;
    }
}

.popup-column {

    &:first-child {
        > div {

            @include breakpoint(tablet) {
                width: 468 / 700 * 100%;
            }
            
        }
    }

    &:nth-child(3) {
        text-align: left;

        @include breakpoint(tablet) {
            padding: 0 0 0 rem(25px);
        }
        
    }

    @include breakpoint(tablet) {
        display: inline-block;
        vertical-align: middle;
    }
    
}

.fd__pmenu-list {
    padding: 0 rem(20px);
}

.fd__menu {
    text-align: center;
    white-space: normal;

    &.header-lang {
        text-align: center;

        .m-item {
            display: inline-block;
            vertical-align: middle;
            margin-bottom: 0;
            font-size: 14px;

            @include breakpoint(tablet) {
                font-size: 18px;
            }

            &:after {
                content: '\\';
                display: inline-block;
                margin: 0 rem(20px);
            }

            &:last-child {
                &:after {
                    content: none;
                }
            }
        }
    }

    @include breakpoint(tablet) {
        text-align: left;
    }
}

.m-item {
    display: block;
    text-transform: uppercase;
    color: $color-dark-text;
    font-family: $font-verdana;
    font-size: 15px;
    line-height: 18px;

    margin: 0 0 26px;

    @include breakpoint(tablet) {
        font-size: 18px;
        line-height: 1;
    }

    &.active {
        color: $color-dark-bold;
    }

    a {
        margin: 0 0 rem(31px);
    }

    &:before {
        content: normal;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

.pop-btn-close {
    $hoverEasing: ease-in-out;
    $hoverDuration: 0.2s;

    @extend %menu-button-size-and-position;
    @extend %align-middle;

    margin: auto;
    position: absolute;
    display: inline-block;
    overflow: hidden;
    padding: 0;
    cursor: pointer;
    opacity: 0;
    transition: opacity $hoverEasing $hoverDuration;
    text-align: center;
    will-change: opacity;

    span {
        $size: 36px;

        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: $size;
        height: $size;

        &:after,
        &:before {
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            height: 1px;
            top: 0;
            bottom: 0;
            margin: auto;
            background: #fff;
            transition: 
                left $hoverEasing $hoverDuration, 
                right $hoverEasing $hoverDuration;

            will-change: left, right;
        }

        &:before {
            transform: rotate(45deg);
        }

        &:after {
            transform: rotate(-45deg);
        }
    }

    &:hover {
        span {

            &:after,
            &:before {
                left: -5px;
                right: -5px;
            }

        }
    }
}