.our-works {
    background: url(../img/bg/our-works-bg-tablet.jpg) center top;
    background-size: cover;
    overflow: hidden;

    @include breakpoint(desktop) {
        background-image: url(../img/bg/our-works-bg.jpg);
        background-size: auto 100%;
    }
    
    h2 {
        @extend %second-header-light;
    }

    h3 {
        @extend %third-header-white;
    }

}

.our-works__wrap {
    overflow: hidden;
    position: relative;
    padding: rem(62px) 0 rem(51px);

    @include breakpoint(desktop) {
        padding-right: rem(20px);
        padding-left: rem(20px);
    }

    ul {
        margin: -10px;

        &:nth-child(2) {
            padding-top: 10px;
        }

        @include breakpoint(desktop) {    
            white-space: nowrap;
        }
    }

    li {
        vertical-align: top;
        padding: 10px;
        
        @include breakpoint(tablet) {
            width: 461px;
            margin-left: auto;
            margin-right: auto;
        }

        @include breakpoint(desktop) {
            display: inline-block;
            white-space: normal;
            width: 100 / 3 * 1%;
        }

        @include breakpoint(large) {
            width: 25%;
        }

        &.long {
            @include breakpoint(desktop) {
                width: (100 - (100 / 3)) * 1%;
            }

            @include breakpoint(large) {
                width: 50%;
            }
        }

        img {
            height: 100%;
            max-width: none;
            
            @supports (object-fit: cover) {
                width: 100%;
                object-fit: cover;

                @include breakpoint(smalltill){
                    height: auto;
                }
            }
        }

        a {
            display: block;
            position: relative;
            overflow: hidden;
            height: auto;
            //height: 192px;

            @include breakpoint(tablet) {
                height: 275px;
            }

            &:hover {
                @include breakpoint(desktop) {
                    > div {
                        > span {
                            color: #fff;
                        }
                    }
                }
            }

            & > div {
                position: absolute;
                right: 0;
                bottom: 0;
                background: rgba(#1f1f1f, 0.9);
                padding: 5px 55px 5px 26px;
                min-height: 60px;
                display: flex;
                align-items: center;

                &:before {
                    content: '';
                    position: absolute;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    border-top: 30px solid transparent;
                    border-bottom: 30px solid transparent;
                    border-right: 29px solid #1e1e1e;
                }

                &:after {
                    @extend .icon;
                    @include sprite($project-arrow);

                    content: '';
                    position: absolute;
                    right: 5px;
                    bottom: 0;
                    top: 0;
                    margin: auto;
                }

                span {
                    @extend %small-text;
                    display: inline-block;
                    vertical-align: middle;
                    color: $color-dark-text;
                    font-weight: 400;
                    line-height: 1.54;

                    @include breakpoint(desktop) {
                        transition: color 0.3s;
                    }
                }
            }
        }
    }
}


.our-works_type2 {

    .our-works__wrap {


        li {
            a {
                & > div {
                    display: block;
                    left: 0;
                    right: 0;
                    top: auto;
                    bottom: 0;
                    text-align: left;
                    padding: 0;
                    background: none;
                    min-height: 0;
                    //transform: translateY(40px);
                    //transition: max-height ease-out 1s, height ease-out 1s;

                    span {
                        display: block;
                        opacity: 0.23;
                        transition: opacity $gEasing $gDuration;

                        &:first-child {
                            opacity: 0.7;
                        }

                        @include breakpoint(smalltill){
                            opacity: 1;
                            &:first-child {
                                opacity: 1;
                            }
                        }
                    }

                    &:after,
                    &:before {
                        content: normal;
                    }
                }

                &:hover {
                    .our-works__descr {
                        top: 0;
                        transform: translateY(0);
                        padding-top: 22px;
                        padding-bottom: 22px;
                        &:before,
                        &:after {
                            opacity: 1;
                        }

                    }
                    span {
                        opacity: 1;
                        &:first-child {
                            opacity: 1;
                        }
                    }
                }
            }

            .our-works__descr {
                position: relative;
                box-sizing: border-box;
                background: rgba(#090909, 0.9);
                top: 0;
                transform: translateY(0);
                padding: 22px;

                &:before {
                    content: '';
                    position: absolute;
                    right: 0;
                    bottom: 0;
                    border-top: 30px solid transparent;
                    border-bottom: 30px solid transparent;
                    border-right: 30px solid #1e1e1e;
                }

                &:after {
                    @extend .icon;
                    @include sprite($project-arrow);
                    content: '';
                    position: absolute;
                    right: 5px;
                    bottom: 25px;
                    margin: auto;
                }

                &:before,
                &:after {
                    opacity: 1;

                    @include breakpoint(small){
                        transition: opacity $gEasing $gDuration;
                        opacity: 0;
                    }
                }

                @include breakpoint(small){
                    padding: 5px 22px 40px 22px;
                    transform-position: center bottom center;
                    transform: translateY(100%);
                    top: -40px;
                    transition: transform $gEasing $gDuration, top $gEasing $gDuration, padding-top $gEasing $gDuration, padding-bottom $gEasing $gDuration;
                }


            }
        }
    }
}

.our-works-footer {
    text-align: center;
    padding: rem(68px) 0 0 0;
    font-size: 11px;
    line-height: 15px;
    font-family: $font-verdana;
    color: $color-light-text;

    .button {
        margin-bottom: rem(23px);
    }

    p {
        @extend %normal-text;
    }

    @include breakpoint(tablet){
        font-size: rem(15px);
    }
}

.our-works_vert {

    & > h2 {
        &:after {
            content: normal;
        }
    }

    .our-works__wrap {

        $margin: 22px;
        margin-left: rem($margin);
        margin-right: rem($margin);
        padding-top: 0;

        li {
            width: 300px;
            box-sizing: border-box;
            display: block;
            margin: 0 auto;

            &:nth-child(5) {
                //display: none;
                visibility: hidden;
                position: absolute;
            }

            a {
                //height: 380px;
                height: auto;

                @include breakpoint(tablet) {
                    height: 32.9827vw;
                }

                @include breakpoint(desktop) {
                    height: 24.7395vw;
                }

                @include breakpoint(large) {
                    height: 19.7916vw;
                }
            }

            @include breakpoint(tablet) {
                width: 33.33%;
                display: inline-block;

                &:nth-child(4) {
                    visibility: hidden;
                    position: absolute;
                }
                &:nth-child(5) {
                    visibility: hidden;
                    position: absolute;
                }
            }

            @include breakpoint(desktop) {
                width: 25%;
                padding-left: rem(8px);
                padding-right: rem(8px);
                &:nth-child(4) {
                    visibility: visible;
                    position: relative;
                    display: inline-block;
                }
                &:nth-child(5) {
                    visibility: hidden;
                    position: absolute;
                }
            }
            @include breakpoint(large) {
                width: 20%;
                padding-left: rem(8px);
                padding-right: rem(8px);

                &:nth-child(5) {
                    visibility: visible;
                    display: inline-block;
                }
            }
        }

        @include breakpoint(tablet) {
            $margin: 62px;
            margin-left: rem($margin);
            margin-right: rem($margin);
        }

        @include breakpoint(desktop) {
            $margin: 105px;
            margin-left: rem($margin);
            margin-right: rem($margin);
        }

        @include breakpoint(large) {
            $margin: 158px;
            margin-left: rem($margin);
            margin-right: rem($margin);
        }
    }
}


.our-works-menu {

    text-transform: uppercase;
    text-align: center;
    position: relative;
    z-index: 1;

    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        margin: auto;
        width: 200px;
        height: 1px;
        background: $color-light-light;
    }
}
.our-works-menu-list {
    font-size: 0;
    padding: 25px 0;
    @include breakpoint(tablet){
        padding: rem(42px) 0;
    }
    @include breakpoint(large) {
        padding: rem(50px) 0;
    }
}
.our-works-menu-item {
    font-size: 12px;
    display: inline-block;
    vertical-align: middle;
    margin: 10px 0;
    font-weight: 300;
    width: 50%;
    text-align: center;


    a {
        color: $color-light-light;

        &:hover {
            color: $color-light-header;
        }
    }

    &.active {
        font-weight: 500;
        a {
            cursor: default;
            color: $color-light-header;
        }
    }

    @include breakpoint(small){
        width: auto;
        margin: 0 14px;
    }

    @include breakpoint(tablet){
        font-size: rem(20px);
        margin: 0 rem(23px);
    }
    @include breakpoint(large) {
        font-size: rem(24px);
        margin: 0 rem(28px);
    }
}


