.fd_work_list_hide {

    li {
        /*animation-name: collapse;
        animation-duration: 0.5s;
        animation-direction: alternate;
        animation-timing-function: ease-in-out;
        animation-fill-mode: forwards;*/
    }

    li {
        will-change: transform, opacity;
        transform: translate3d(0, 0, 0);
        transition: transform $gEasing $gDuration, opacity $gEasing $gDuration;
    }
    $delay: 0.05s;
    ul:first-child {
        @for $i from 1 through 5 {
            li:nth-child(#{$i}) {
                transition-delay: $i * $delay;
            }
        }
    }
    ul:nth-child(2) {
        @for $i from 6 through 10 {
            li:nth-child(#{$i - 5}) {
                transition-delay: $i * $delay;
            }
        }
    }
}

.fd_work_list_show {
    $delay: 0.2s;
    li {
        /*animation-name: collapse;
        animation-duration: 0.5s;
        animation-direction: alternate;
        animation-timing-function: ease-in-out;
        animation-fill-mode: forwards;*/
    }
    ul:first-child {
        @for $i from 1 through 5 {
            li:nth-child(#{$i}) {
                animation-delay: $i * $delay;
            }
        }
    }
    ul:nth-child(2) {
        @for $i from 6 through 10 {
            li:nth-child(#{$i - 5}) {
                animation-delay: $i * $delay;
            }
        }
    }

    li {
        will-change: transform, opacity;
        transform: translate3d(0, 0, 0);
        transition: transform $gEasing $gDuration, opacity $gEasing $gDuration;
    }
    $delay: 0.05s;
    ul:first-child {
        @for $i from 1 through 5 {
            li:nth-child(#{$i}) {
                transition-delay: $i * $delay;
            }
        }
    }
    ul:nth-child(2) {
        @for $i from 6 through 10 {
            li:nth-child(#{$i - 5}) {
                transition-delay: $i * $delay;
            }
        }
    }
}

.static_works {
    position: relative;
}
.dynamic_works {
    position: absolute;
    left: 0;
    top: 0;
    display: none;
}
.our-works_notitle {
    .our-works__wrap {
        padding-top: rem(25px);
        padding-bottom: rem(25px);
    }
}