@function em($px, $base: $root-font-size) {
    @return ($px / $base) * 1em;
}

@function rem($px, $base: $root-font-size) {
    @return ($px / $base) * 1rem;
}

@function vw($px, $base: $view-port-width) {
    @return ($px / $base) * 1vw;
}

@function vh($px, $base: $view-port-height) {
    @return ($px / $base) * 1vh;
}

