/*********************
BREAKPOINTS
*********************/
/*
usage example:
---------------
breakpoint(small) will generate:            @media (min-width: 640px)
breakpoint(smalltill) will generate:        @media (max-width: 639px)
breakpoint(smallonly) will generate:        @media (min-width: 640px) and (max-width: 767px)
breakpoint((small, xlarge)) will generate:  @media (min-width: 640px) and (max-width: 1279px)
*/
@mixin breakpoint($point) {
    $key: 1;
    $points: (
                    (xsmall, 320px),
                    (small, 640px),
                    (tablet, 748px),
                    (desktop, 1024px),
                    (xdesktop, 1280px),
                    (large, 1500px)
    );

    @if $point == landscape {
        @media (min-aspect-ratio: 13/9) {
            @content;
        }
    }
    @else if $point == portrait {
        @media (max-aspect-ratio: 13/9) {
            @content;
        }
    }
    @else if ($point == retina) {

        @media (-webkit-min-device-pixel-ratio: 2), (-moz-device-pixel-ratio: 2), (-o-min-device-pixel-ratio: 2/1), (min-device-pixel-ratio: 2) {
            @content ;
        }

    } @else if ($point == ios) {
        @media
        only screen
        and (max-device-width: 1024px)
        and (min-device-height: 768px),

        only screen
        and (min-device-width : 375px)
        and (max-device-width : 667px),

        only screen
        and (min-device-width : 414px)
        and (max-device-width : 736px),

        only screen
        and (min-device-width : 320px)
        and (max-device-width : 568px),

        only screen
        and (min-device-width : 320px)
        and (max-device-width : 480px) {
            @content;
        }

    } @else if (length($point) == 2) {
        $media1: null;
        $media2: null;
        @each $pt1 in $points {
            @if nth($point, 1) == nth($pt1, 1) {
                $media1: nth($pt1, 2);
            }
        }
        @each $pt2 in $points {
            @if nth($point, 2) == nth($pt2, 1) {
                $media2: nth($pt2, 2) - 1px;
            }
        }
        @if ($media1) {
            @if ($media2) {
                @media (min-width: $media1) and (max-width: $media2) {
                    @content;
                }
            }
        }
    } @else {

        @each $pt in $points {
            // from
            @if $point == nth($pt, 1) {
                @media (min-width: nth($pt, 2)) {
                    @content;
                }
            }
            // to
            @if ($point == unquote(#{nth($pt, 1)}till)) {
                @media (max-width: nth($pt, 2) - 1) {
                    @content;
                }
            }
            // only
            @if ($point == unquote(#{nth($pt, 1)}only)) {
                // check for next
                @if length($points) > $key {
                    $item: nth($points, $key + 1);
                    @media (min-width: nth($pt, 2)) and (max-width: nth($item, 2) - 1) {
                        @content;
                    }
                } @else {
                    @media (min-width: nth($pt, 2)) {
                        @content;
                    }
                }
            }
            $key: $key + 1;
        }
    }
}

@mixin line($height: 2px){
    content: "";
    height: $height;
    border-radius: $height;
    background: $fontColor3;
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    margin-top: round(-$height / 2);
};

@mixin queue($elems: 10) {
    $tEasing: ease-out;
    $tDuration: 0.5s;
    $elements: $elems;
    $offset: 200px;
    $delay: 0.1s;

    .queue {
        transition: opacity $tEasing 0s, transform $tEasing 0s;
    }
    .queue {
        &.fd__bot-stripe,
        &.fd__top-stripe {
            transition: height $tEasing 0s;
        }
    }

    &.animate {
        .queue {
            transition-duration: $tDuration;
        }
    }

    &.queueFromBottom {
        @for $i from 1 through $elements {
            .queue#{$i} {
                opacity: 0;
                transform: translate3d(0, $offset, 0);
                transition-delay: $delay * ($elements - $i);
            }
        }
        .fd__bot-stripe.queue,
        .fd__top-stripe.queue {
            opacity: 1;
            transform: translate3d(0, 0, 0);
            height: 0;
        }

        $line: 0;
        &.in {
            .queue {
                transition-duration: ($tDuration);
            }
            @for $i from 1 through $elements {
                .queue#{$i} {
                    opacity: 1;
                    transform: translate3d(0, 0, 0);
                    transition-delay: $delay * $i;
                }
            }
            .fd__bot-stripe.queue,
            .fd__top-stripe.queue {
                height: 0;

                transform: translate3d(0, 0, 0);

                @include breakpoint(tablet) {
                    height: 60px;
                }

                @media (max-height: 470px) {
                    display: none;
                }
            }
        }
    }

    &.queueToBottom {
        $offset: 60px;
        @for $i from 1 through $elements {
            .queue#{$i} {
                opacity: 1;
                transform: translate3d(0, 0, 0);
                transition-delay: $delay * ($elements - $i);
            }
        }

        &.in {
            @for $i from 1 through $elements {
                .queue#{$i} {
                    opacity: 0;
                    transform: translate3d(0, $offset, 0);
                    transition-delay: $delay * $i;
                }
            }
        }

    }

    &.queueFromTop {
        @for $i from 1 through $elements {
            .queue#{$i} {
                opacity: 0;
                transform: translate3d(0, -$offset, 0);
                transition-delay: $delay * ($elements - $i);
            }
        }
        &.in {
            @for $i from 1 through $elements {
                .queue#{$i} {
                    opacity: 1;
                    transform: translate3d(0, 0, 0);
                    transition-delay: $delay * $i;
                }
            }
        }
    }

    &.queueToTop {
        @for $i from 1 through $elements {
            .queue#{$i} {
                opacity: 1;
                transform: translate3d(0, 0, 0);
            }
        }

        &.in {
            @for $i from 1 through $elements {
                .queue#{$i} {
                    opacity: 0;
                    transform: translate3d(0, -$offset, 0);
                    transition-delay: $delay * $i;
                }
            }
        }
    }

    &.queueFromRight {
        $offset: 50px;
        //$tDuration: 0.7s;
        //$delay: 0.3s;
        $tEasing: linear;
        @for $i from 1 through $elements {
            .queue#{$i} {
                opacity: 0;
                transform: translate3d($offset, 0, 0);
                transition-delay: $delay * ($elements - $i);
            }
        }
        .fd__bot-stripe.queue,
        .fd__top-stripe.queue {
            opacity: 1;
            transform: translate3d(0, 0, 0);
            height: 0;
        }

        $line: 0;
        &.in {
            .queue {
                transition-duration: ($tDuration);
            }
            @for $i from 1 through $elements {
                .queue#{$i} {
                    opacity: 1;
                    transform: translate3d(0, 0, 0);
                    transition-delay: $delay * $i;
                }
            }
        }
    }

    &.queueFromLeft {
        $offset: 50px;
        //$tDuration: 0.7s;
        //$delay: 0.3s;
        $tEasing: linear;
        @for $i from 1 through $elements {
            .queue#{$i} {
                opacity: 0;
                transform: translate3d(-$offset, 0, 0);
                transition-delay: $delay * ($elements - $i);
            }
        }
        .fd__bot-stripe.queue,
        .fd__top-stripe.queue {
            opacity: 1;
            transform: translate3d(0, 0, 0);
            height: 0;
        }

        $line: 0;
        &.in {
            .queue {
                transition-duration: ($tDuration);
            }
            @for $i from 1 through $elements {
                .queue#{$i} {
                    opacity: 1;
                    transform: translate3d(0, 0, 0);
                    transition-delay: $delay * $i;
                }
            }
        }
    }
};
