@import "functions";
@import "typography";

$view-port-width: 1920 / 100 * 1px;
$view-port-height: 1080 / 100 * 1px;

$v-height: 950 / 100 * 1px, 640 / 100 * 1px, 550 / 100 * 1px, 480 / 100 * 1px;

$header-height: 0;
$menu-height: 0;
$footer-height: 0;
$sidebar-width: 0;
$popup-width: 0;

$min-width: 320px;
$max-width: none;

$slider-step-count: 1;

// sizes

$root-font-size: 115px;
$font-size-h1: 115px, 95px, 95px, 48px;
$font-size-h1-2: 75px, 62px, 62px, 37px;
$font-size-h2: 36px, 30px, 30px, 18px;
$font-size-h3: 24px, 20px, 20px, 12px;
$font-size: 15px, 13px, 13px, 11px;
$line-height: 22px, 20px, 20px, 15px;
$font-size-sm: 13px, 11px, 11px, 10px;
$scale: 1, 0.83, 0.83, 0.5;

// colors

$color-placeholder: #555;
$color-link: #999;
$color-link-hover: #fff;

$color-light-light: #888d9d;
$color-light-text: #585d6b;
$color-light-header: #3d556d;
$color-light-background: #fff;

$color-dark-background: #090909;
$color-dark-bold: #fff;
$color-dark-header: #777;
$color-dark-light: #555;
$color-dark-text: #999;

$color-input-background: #1e1e1e;
$color-input-background-focus: #2a2b2c;
$color-input-background-invalid: #7f0102;

$color1: #bbbec7;

$colors: (
    'light': (
        'background': $color-light-background,
        'header': $color-light-header,
        'light': $color-light-light,
        'text': $color-light-text
    ),
    'dark': (
        'background': $color-dark-background,
        'bold': $color-dark-bold,
        'header': $color-dark-header,
        'light': $color-dark-light,
        'text': $color-dark-text
    )
);

@function color($name, $variant: 'text') {
    $color: null;
    $color-spectrum: map-get($colors, $name);

    @if $color-spectrum {
        $color: map-get($color-spectrum, $variant);
    }

    @return $color;
}

@function s($px, $base: 1) {
    @return $px * nth($scale, $base);
}

$resolution: large, desktop, tablet;
$color: color('dark');
$bg-color: color('dark', 'background');

$gEasing: ease-out;
$gDuration: 0.5s;

%clear-fix {
    &:after {
        content: '';
        display: block;
        height: 0;
        clear: both;
    }
}

%align-middle {
    &:after {
        content: '';
        display: inline-block;
        vertical-align: middle;
        height: 100%;
    }
}

%align-middle-before {
    &:before {
        content: '';
        display: inline-block;
        vertical-align: middle;
        height: 100%;
    }
}

%align-bottom {
    &:after {
        content: '';
        display: inline-block;
        vertical-align: bottom;
        height: 100%;
    }
}

%default-link {
    color: $color-link;
    text-decoration: none;

    @include breakpoint(desktop) {
        transition: color 0.3s;
    }

    &:hover {
        @include breakpoint(desktop) {
            color: #fff;
        }
    }
}

%button {
    display: inline-block;
    vertical-align: middle;
    color: #fff;
    text-decoration: none;
    cursor: pointer;
    line-height: 1;
    white-space: nowrap;
    outline: none;
    text-align: center;
    user-select: none;
    border: 2px solid;
    text-transform: uppercase;
    padding: 9px 43px;
    min-width: 120px;
    transition: opacity 0.5s;
    text-align: center;

    @include breakpoint(tablet) {
        border: rem(4px) solid;
        min-width: rem(240px);
        padding: rem(18px) rem(85px);
    }

    @include breakpoint(desktop) {
        transition: background-color 0.3s, border-color 0.3s, opacity 0.5s;
    }

    @include breakpoint(large) {
        //padding: 0 rem(34px);
        padding: rem(18px) rem(85px);

    }

    @extend %align-middle;

    &.hidden {
        opacity: 0;
    }

    span {
        vertical-align: middle;
        display: inline-block;
        text-transform: uppercase;
        white-space: normal;
        font-family: $font-verdana;
        font-weight: 700;
        @extend %page-text;

        @include breakpoint(desktop) {
            transition: color 0.3s;
        }
    }
}

%input {
    height: 48px;
    line-height: 1;
    background-color: $color-input-background;
    color: color('dark');
    font-weight: 400;
    padding: 0 rem(24px);
    width: 100%;
    appearance: none;
    font-size: rem(15px);
    transition: background-color 0.5s;

    &:focus {
        background: $color-input-background-focus;

        &::-webkit-input-placeholder {
        }

        &:-ms-input-placeholder {
        }

        &::-moz-placeholder {
        }

        &.placeholder {
            //color: transparent;
        }
    }

    &:invalid {
        background: $color-input-background-invalid;

        &:focus {
            background: $color-input-background-focus;
        }
    }

    @include breakpoint(tablet) {
        height: rem(48px);
    }
}

%scroll-thumb {
    width: 6px;
    background-color: #c1c1c1;
    border-radius: 3px;
}

%custom-scroll {
    &::-webkit-scrollbar {
        width: 6px;
        background-color: transparent;
    }

    &::-webkit-scrollbar-thumb {
        @extend %scroll-thumb;
    }
}

%page-header {
    text-transform: uppercase;
    letter-spacing: rem(-5px);
    font-weight: bold;
    font-size: nth($font-size-h1, 4);

    & + p {
        padding-top: vh(31px, nth($v-height, 4)); // 42

        @include breakpoint(tablet) {
            padding-top: vh(42px, nth($v-height, 3));// 62
        }

        @include breakpoint(desktop) {
            padding-top: vh(42px, nth($v-height, 2)); // 62
        }

        @include breakpoint(large) {
            padding-top: vh(74px, nth($v-height, 1)); //95
        }

    }

    &.fd__title_type1 {
        font-size: 42px;


        &.fd__title_xs-esmall {
            @include breakpoint(smalltill){
                font-size: 27px;
            }
        }


        @include breakpoint(tablet) {
            font-size: nth($font-size-h1-2, 3);
        }

        @include breakpoint(desktop) {
            font-size: nth($font-size-h1-2, 2);
        }


        @include breakpoint(large) {
            font-size: nth($font-size-h1-2, 1);
            line-height: nth($font-size-h1-2, 1) + 5px;
        }
    }

    @include breakpoint(tablet) {
        font-size: nth($font-size-h1, 3);
    }

    @include breakpoint(desktop) {
        font-size: nth($font-size-h1, 2);
    }

    @include breakpoint(large) {
        font-size: nth($font-size-h1, 1);
    }
}

%page-second-header {
    text-transform: uppercase;
    letter-spacing: rem(-2px);
    font-weight: bold;
    padding-top: rem(100px);

    font-size: nth($font-size-h2, 4);

    @include breakpoint(tablet) {
        font-size: nth($font-size-h2, 3);
    }

    @include breakpoint(desktop) {
        font-size: nth($font-size-h2, 2);
    }

    @include breakpoint(large) {
        font-size: nth($font-size-h2, 1);
    }

    &:first-child {
        padding-top: rem(75px);
    }

    & + p,
    & + h3 {
        padding-top: rem(55px);
    }
}

%page-third-header {
    text-transform: uppercase;
    letter-spacing: rem(-1px);
    font-weight: 500;

    font-size: nth($font-size-h3, 4);

    @include breakpoint(tablet) {
        font-size: nth($font-size-h3, 3);
    }

    @include breakpoint(desktop) {
        font-size: nth($font-size-h3, 2);
    }

    @include breakpoint(large) {
        font-size: nth($font-size-h3, 1);
    }

    & + p {
        padding-top: rem(22px);
    };
}

%page-text {
    font-family: $font-verdana;

    font-size: nth($font-size, 4);
    line-height: nth($line-height, 4);

    @include breakpoint(tablet) {
        font-size: nth($font-size, 3);
        line-height: nth($line-height, 3);
    }

    @include breakpoint(desktop) {
        font-size: nth($font-size, 2);
        line-height: nth($line-height, 2);
    }

    @include breakpoint(large) {
        font-size: nth($font-size, 1);
        line-height: nth($line-height, 1);
    }

    & + p,
    & + aside {
        margin-top: rem(30px);

        & + aside {
            margin-top: rem(30px);
        }
        
        img {
            margin-top: rem(-10px);
        }
    }

    & + ul {
        margin-top: rem(30px);
    }

    & + img {
        margin-top: rem(20px);
    }

    & + li {
        margin-top: rem(17px);
    }
}

%normal-text {
    font-family: $font-verdana;

    font-size: nth($font-size, 4);
    line-height: nth($line-height, 4);

    @include breakpoint(tablet) {
        font-size: nth($font-size, 3);
        line-height: nth($line-height, 3);
    }

    @include breakpoint(desktop) {
        font-size: nth($font-size, 2);
        line-height: nth($line-height, 2);
    }

    @include breakpoint(large) {
        font-size: nth($font-size, 1);
        line-height: nth($line-height, 1);
    }
}

%small-text {
    font-family: $font-verdana;
    font-size: nth($font-size-sm, 4);

    @include breakpoint(tablet) {
        font-size: nth($font-size-sm, 3);
    }

    @include breakpoint(desktop) {
        font-size: nth($font-size-sm, 2);
    }

    @include breakpoint(large) {
        font-size: nth($font-size-sm, 1);
    }
}

%li-bullet {
    &:before {
        content: '';
        width: rem(15px);
        height: 1px;
        background: #697c8e;
        display: inline-block;
        vertical-align: middle;
        margin: 0 rem(13px) 0 0;
    }
}

%inner-layout {
    padding: 0 12px;

    @include breakpoint(tablet) {
        padding: 0 22px;
    }

    @include breakpoint(desktop) {
        padding: 0 51px;
    }

    @include breakpoint(large) {
        padding: 0 239px / 1920px * 100%;
    }
}

%list-item {

    & + li {
        margin-top: rem(27px);
    }


}

.custom-scroll-bar {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: #ffffff;
}

.custom-scroll-button {
    @extend %scroll-thumb;
    position: absolute;
    right: 0;
    opacity: 0;

    &.active {
        opacity: 1;
    }
}

.article {
    section {
        border-bottom: 1px solid $color-light-light;
        padding: 0 0 rem(55px);

        &:last-child {
            border-bottom: none;
        }
    }
}

%menu-button-size-and-position {
    width: 56px;
    height: 50px;
    top: 18px;
    right: 10px;
    
    @include breakpoint(tablet) {
        top: rem(50px);
        right: rem(50px);
        width: 64px;
        height: 62px;
    }
}

.fd_align-center {
    text-align: center;
}
.fd_align-left {
    text-align: left;
}
.fd_align-right {
    text-align: right;
}
.fd_align-justify {
    text-align: justify;
}