
.offer {
    @extend %inner-layout;
    background-color: #2d3e50;
    position: relative;
    z-index: 1;

    @media (max-width: 332px) {
        padding-left: 0;
        padding-right: 0;
    }

    &.mesh {
        &:after {
            z-index: -1;
        }
    }

    h2 {
        @extend %second-header-dark;
        vertical-align: middle;
        color: $color-dark-bold;
        padding: 0;
        display: block;
        line-height: 1.39;
        margin: 0 0 39px;

        &:first-child {
            padding: 0 15px;
        }

        @include breakpoint(tablet) {
            max-width: 460px;
            margin: 0 auto 45px;
        }

        @include breakpoint(desktop) {
            max-width: none;
        }

        @include breakpoint(large) {
            display: inline-block;
            max-width: none;
            margin: 0;
            text-align: left;
        }

        &:after {
            content: none;
        }
    }

    .layout {
        min-width: 0;
        text-align: center;
        padding: rem(76px) 0 rem(91px);

        &.fd_align-center {
            justify-content: center;
            text-align: center;
        }

        @include breakpoint(large) {
            text-align: left;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

    }

    .button {
        border-color: rgba($color-dark-bold, 0.57);
    }
}