.fd__digits {
    background: $color-light-background;
    text-align: center;
    color: $color-light-header;

    padding-top: rem(75px);

    h2 {
        letter-spacing: -1px;
    }

    @include breakpoint(desktop) {
        padding-bottom: rem(190px);
    }
}
.fd__digits-item {
    font-size: 11px;
    line-height: 15px;
    font-family: $font-verdana;

    @include breakpoint(small){
        font-size: rem(15px);
    }
    @include breakpoint(desktoptill){
        margin-bottom: rem(94px);
    }
}
.fd__digits-title {
    font-size: nth($font-size-h2, 4);
}
.fd__digits-value {
    font-weight: 200;
    font-size: rem(122px);
    line-height: rem(122px);
    padding-top: rem(1px);
    padding-bottom: rem(6px);
    font-family: $font;
}

.fd__digits-row3 {
    font-size: 0;
    line-height: 0;
    white-space: nowrap;
    padding-top: rem(47px);

    &:before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        height: 100%;
    }

    & > *:last-child {
        margin-bottom: 0;
    }

    @include breakpoint(desktop){
        padding-bottom: rem(94px);
    }
}
.fd__digits-cell {
    max-width: 100%;
    overflow: hidden;

    vertical-align: middle;
    white-space: normal;
    font-size: rem(15px);
    line-height: rem(15px);

    @include breakpoint(desktop){
        display: inline-block;
        width: 30%;
    }

    @include breakpoint(large){
        width: 23.6%;
    }
}