.cooperation {
    background: $color-light-background;
    overflow: hidden;

    .layout {
        > h2 {
            @extend %second-header-light;

            &:first-child {
                padding-top: rem(140px);
            }

            span {
                margin: 0 0 rem(10px);
            }

        }

        > h3 {
            @extend %third-header-white;
        }
    }


}

.cooperation__list-wrap {
    position: relative;
    text-align: center;
    padding: rem(88px) 0 rem(70px) 0;
}

.cooperation__list {
    $right-border: 120px / 1920px * 100%;
    padding: 0 41px 28px;

    /*@include breakpoint(tablet) {
        padding: 0 37px 49px 34px;
    }

    @include breakpoint(desktop) {
        padding: 0 60px 26px 68px;
    }

    @include breakpoint(large) {
        padding: 0 150px / 1920px * 100% 26px 347px / 1920px * 100%;
    }*/
}

.cooperation__item {

    vertical-align: top;
    display: block;
    margin: 0 auto 34px auto;

    h3 {
        @extend %page-third-header;
        color: $color-light-header;
    }

    p {
        @extend %page-text;
        color: $color-light-text;
        position: relative;
        line-height: 1.47;
        padding: 0 0 rem(33px);
    }

    @include breakpoint(tablet) {
        display: inline-block;
        width: rem(348px);
        margin: 0 rem(40px) 34px;
        &:first-child {
            margin-left: 0;
        }
        &:last-child {
            margin-right: 0;
        }
    }

    @include breakpoint(desktop) {
        /*width: rem(348px);
        margin: 0 rem(80px) 34px;*/
        margin: 0 rem(97px) 34px;
        width: rem(420px);
    }

    /*@include breakpoint(xdesktop) {
        margin: 0 rem(97px) 34px;
        width: rem(420px);
    }

    @include breakpoint(large) {
        margin: 0 rem(97px) 34px;
        width: rem(420px);
    }*/
}
